import React, { Component } from 'react'
import './Chronic_ordertable.scss'

class Chronicordertable extends Component {
    // 0:一天 1:三天 2:一週 3:兩週
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    celltable(){
        function changegridnumber(key){
            switch (key) {
                case 0:
                    return 24
                case 1: 
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    break;
            }
        }
        const { chronic_state } = this.props
        const number = changegridnumber(chronic_state)
        var arr = []
        for (let index = 0; index < number; index++) {
            arr.push(
                <div className='cell'></div>
            )
            
        }
        return arr
    }
    render() {
        function changegridstyle(key) {
            switch (key) {
                case 0:
                    return 'ordertableoneday'
                case 1:
                    return 'ordertablethirdday'
                case 2:
                    return 'ordertableoneweek'
                case 3:
                    return 'ordertabletwoweek'
                default:
                    return null
            }
        }
        const { chronic_state } = this.props
        console.log(chronic_state)
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr",position:'relative'}}>
                <div style={{ background: "#CCCCCC", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>醫藥囑</t2>
                </div>
                <div className={changegridstyle(chronic_state)}>{this.celltable()}</div>
                <div style={{position:'absolute',left:140}}>
                    <div style={{ padding: "10px 0px", paddingLeft: "0px" }}>
                        <div style={{ width: "200px", height: "20", backgroundColor: "#3B97E1", color: "white", padding: "0px 5px" }}>Ampolin 500mg/vial</div>
                    </div>
                    <div style={{ padding: "10px 0px", paddingLeft: "0px" }}>
                        <div style={{ width: "300px", height: "20", backgroundColor: "#3B97E1", color: "white", padding: "0px 5px" }}>Decardron 5ml/1ml</div>
                    </div>
                    <div style={{ padding: "10px 0px", paddingLeft: "100px" }}>
                        <div style={{ width: "400px", height: "20", backgroundColor: "rgba(230,230,230,1)", color: "white", padding: "0px 5px" }}>Ampolin 500mg/vial</div>
                    </div>
                    <div style={{ padding: "10px 0px", paddingLeft: "100px" }}>
                        <div style={{ width: "500px", height: "20", backgroundColor: "#3B97E1", color: "white", padding: "0px 5px" }}>Glucose</div>
                    </div>
                    <div style={{ padding: "10px 0px", paddingLeft: "200px" }}>
                        <div style={{ width: "500px", height: "20", backgroundColor: "#3B97E1", color: "white", padding: "0px 5px" }}>Solu-cortef 100mg</div>
                    </div>
                    <div style={{ padding: "10px 0px", paddingLeft: "300px" }}>
                        <div style={{ width: "500px", height: "20", backgroundColor: "#3B97E1", color: "white", padding: "0px 5px" }}>Lasix 20mg/2ml</div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Chronicordertable