import React, { Component } from 'react';
import Prescriptionordertablecell from './Prescriptionordertablecell'
import { Prescriptionordertablelist } from '../../Data/Wardviewdata'
import './Prescriptionordertable.scss'


class Prescriptionordertable extends Component {
    render() {
        let Prescriptionordertabletable = Prescriptionordertablelist.map(
            (info, index) =>
                <Prescriptionordertablecell key={index} info={info}></Prescriptionordertablecell>
        )
        return (
            <div>
                <div className='Prescriptionordertablelayout'>
                    <t3 className='title'>學名</t3>
                    <t3 className='title'>商品名</t3>
                    <t3 className='title'>劑量/單位/途徑/頻次</t3>
                    <t3 className='title'>狀態</t3>
                    <t3 className='title'>開始日期</t3>
                    <t3 className='title'>結束日期</t3>
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {
                        Prescriptionordertabletable
                    }
                </div>
            </div>
        );
    }
}

export default Prescriptionordertable