import React, { Component } from 'react'
import * as d3 from "d3";
import './Watervaluechart.scss'
import { daysdiff } from '../../Function/Timeformat'
import { getRandomfornumber } from '../../Function/Wardstate'

class Watervaluechart extends Component {
    constructor(props) {
        super(props);
        //Update
    }
    componentDidUpdate() {
        const { svg_key } = this.props
        d3.select("#" + svg_key).remove()
        this.drawChart()

    }
    componentDidMount() {
        this.drawChart();
    }

    componentWillUnmount() {
    }

    cal_svg_hight() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 160
            }
            else {
                return 130
            }
        }
        else {
            if (axisBot) {
                return 130
            }
            else {
                return 90
            }
        }
    }

    cal_svg_transform() {
        const { axisTop, axisBot } = this.props
        console.log(axisTop)
        if (axisTop) {
            if (axisBot) {
                return 70
            }
            else {
                return 50
            }
        }
        else {
            if (axisBot) {
                return 5
            }
            else {
                return 5
            }
        }
    }

    drawChart() {
        const { svg_key, circlrcolor, data, axisTop, id_key, axisBot, caseiddays } = this.props
        const width = (window.screen.availWidth - 50) / 2 - 50, height = 400
        const max = 200, min = 0, margin = 20
        const intervalday = daysdiff(new Date().getTime(), new Date(caseiddays).getTime())
        const rectwidth = (width / intervalday) - 2
        var x = d3.scaleLinear()
            .domain([0, intervalday])
            .range([0, width]);


        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([0, 200])
            .range([height, 0])



        var yAxis = d3.axisLeft(y)
            .ticks(11)
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        var xAxisBottom = d3.axisBottom(x)
            .ticks(intervalday)
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) {
                if (d % 10 === 0) {
                    if (d > 0) {
                        return 'D' + d
                    }
                    return null
                }
                else return null;
            });


        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .attr("viewBox", [0, 0, width + 80, height + 50])
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 30 + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");

        svg.append("g")
            .call(xAxisBottom)
            .attr("class", "axis");


        for (let index = 0; index < 11; index++) {
            //上下虛線
            svg.append('line')
                .attr('x1', 0)
                .attr('y1', y(index * 20))
                .attr('x2', width)
                .attr('y2', y(index * 20))
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }

        for (let index = 0; index < intervalday; index++) {
            //上下虛線
            if (index % 10 === 0 && index > 0) {
                svg.append('line')
                    .attr('x1', x(0.5 + index))
                    .attr('y1', y(min))
                    .attr('x2', x(0.5 + index))
                    .attr('y2', y(max))
                    .style('stroke', 'rgba(187, 187, 187, 1)')
                    .style('stroke-width', 1)
                    .style('stroke-dasharray', 5.5);
            }
        }




        function rowdatageneral(number){
            const rowdata = {
                'IV': {
                    "max": getRandomfornumber(number),
                    "min": 0
                },
                'feeding': {
                    "max": getRandomfornumber(number),
                    "min": 0
                },
                'Other': {
                    "max": getRandomfornumber(number),
                    "min": 0
                }
            }
            return rowdata
        }
        for (let index = 1; index < intervalday + 1; index++) {
            const rowdata = rowdatageneral(50)
            var svgrect = svg.append('g').on('click', function () {

                d3.selectAll("rect#A123").remove()
                svgrect.append('rect')
                    .attr('id', 'A123')
                    .attr('width', rectwidth + 2)
                    .attr('height', y(0) - y(rowdata.IV.max + (rowdata.Other.max - rowdata.Other.min) + (rowdata.feeding.max - rowdata.feeding.min)))
                    .attr('x', x(index) - (rectwidth / 2) - 1)
                    .attr('y', y(rowdata.IV.max + (rowdata.Other.max - rowdata.Other.min) + (rowdata.feeding.max - rowdata.feeding.min)))
                    .style('fill', 'rgba(0,0,0,0)')
                    .style('stroke', '#F5A623')
                    .style('stroke-width', 2);
            })
            svgrect.append('line')
                .attr('x1', x(index))
                .attr('y1', y(rowdata.IV.min + (rowdata.Other.max - rowdata.Other.min) + (rowdata.feeding.max - rowdata.feeding.min)))
                .attr('x2', x(index))
                .attr('y2', y(rowdata.IV.max + (rowdata.Other.max - rowdata.Other.min) + (rowdata.feeding.max - rowdata.feeding.min)))
                .style('stroke', '#C3EFFF')
                .style('stroke-width', rectwidth)

            svgrect.append('line')
                .attr('x1', x(index))
                .attr('y1', y(rowdata.feeding.min + (rowdata.Other.max - rowdata.Other.min)))
                .attr('x2', x(index))
                .attr('y2', y(rowdata.feeding.max + (rowdata.Other.max - rowdata.Other.min)))
                .style('stroke', '#2E64A4')
                .style('stroke-width', rectwidth)


            svgrect.append('line')
                .attr('x1', x(index))
                .attr('y1', y(rowdata.Other.min))
                .attr('x2', x(index))
                .attr('y2', y(rowdata.Other.max))
                .style('stroke', '#3B97E1')
                // .style('stroke', 'red')
                .style('stroke-width', rectwidth)
        }
    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default Watervaluechart