import React, { Component } from 'react';
import { Month_format } from '../Commonfunction'

class Bedinformation extends Component {
    state = {
        tableselect: false
    }

    SelectTruechange() {
        this.setState({
            tableselect: true
        })
    }

    SelectFalsechange() {
        this.setState({
            tableselect: false
        })
    }

    sendReturnSelect = (id) => {
        console.log(id)
        this.props.parentCallback(id);
    }

    Selectswitch(id, select) {
        if (id === select) {
            return true
        }
        else return false
    }
    render() {
        function weekdatformat(weekday) {
            switch (weekday) {
                case 0:
                    return "SUN"
                case 1:
                    return "MON"
                case 2:
                    return "TUE"
                case 3:
                    return "WED"
                case 4:
                    return "THU"
                case 5:
                    return "FRI"
                case 6:
                    return "SAT"
                default:
                    return null
            }
        }
        const { selectkey, idkey, localtime, data } = this.props
        const day = Month_format(new Date(localtime).getMonth()) + "/" + new Date(localtime).getDate()
        const weekday = new Date(localtime).getDay()
        const Unselect_tablestyle = { height: "50px", display: "flex", alignItems: "center", justifyContent: "center", border: "1px solid rgba(238,238,238,1)", borderLeftWidth: "0px" }
        const select = this.Selectswitch(selectkey, idkey)
        if (data === null) {
            return (
                <div style={select ? { border: "1px solid rgba(59, 151, 225, 1)", marginTop: '-2px' } : { cursor: "pointer" }} onClick={() => this.sendReturnSelect({ "idkey": idkey, "time": localtime })}>
                    <div style={Unselect_tablestyle}>
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                            <t2 style={{ textAlign: "center" }}>{day}</t2>
                            <t2 style={{ textAlign: "center" }}>{weekdatformat(weekday)}</t2>
                        </div>
                    </div>
                    <div style={Unselect_tablestyle}>
                        <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                            <t2 style={{ textAlign: "center" }}>0(12)</t2>
                            <t2 style={{ textAlign: "center" }}>--%</t2>
                        </div>
                    </div>
                    <t2 style={Unselect_tablestyle}>--</t2>
                    <t2 style={Unselect_tablestyle}>--</t2>
                    <t2 style={Unselect_tablestyle}>--</t2>
                    <t2 style={Unselect_tablestyle}>--</t2>
                    <t2 style={Unselect_tablestyle}>--</t2>
                </div>
            );

        }
        return (
            <div style={select ? { border: "1px solid rgba(59, 151, 225, 1)", marginTop: '-2px' } : { cursor: "pointer" }} onClick={() => this.sendReturnSelect({ "idkey": idkey, "time": localtime })}>
                <div style={Unselect_tablestyle}>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                        <t2 style={{ textAlign: "center" }}>{day}</t2>
                        <t2 style={{ textAlign: "center" }}>{weekdatformat(weekday)}</t2>
                    </div>
                </div>
                <div style={Unselect_tablestyle}>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                        <t2 style={{ textAlign: "center" }}>{data.signBed}(12)</t2>
                        <t2 style={{ textAlign: "center" }}>{parseInt(parseInt(data.signBed) * 100 / 12)}%</t2>
                    </div>
                </div>
                <t2 style={Unselect_tablestyle}>{data.transferOutNum}</t2>
                <t2 style={Unselect_tablestyle}>{data.transferInNum}</t2>
                <t2 style={Unselect_tablestyle}>{data.invasiveRespirator}</t2>
                <t2 style={Unselect_tablestyle}>{data.nonivasiveRespirator}</t2>
                <t2 style={Unselect_tablestyle}>{data.TPN}</t2>
            </div>
        );
    }
}

export default Bedinformation