import React, { Component } from 'react';
import IOpage from '../../Module/iopage'

class Crossection_io extends Component {
    render() {
        const { userdata, title, PickerDate } = this.props
        const titlearray = ['白班', '小夜', '大夜', '全日']
        const titlelab = titlearray.map(function (info, index) {
            return (
                <centerstyle key={index}>
                    <t2>{info}</t2>
                </centerstyle>
            );
        })
        return (
            //510
            <div>
                <div style={{ lineHeight: "50px", backgroundColor: "#cccccc", padding: "0px 5px" }}>
                    <t1>{title}</t1>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr", height: "40px" }}>
                    <div></div>
                    {titlelab}
                </div>
                <IOpage PickerDate={PickerDate} userdata={userdata} iostate={0}></IOpage>
            </div>
        );
    }
}

export default Crossection_io