import React, { Component } from 'react';
import { bedInfo } from '../Data/Wardviewdata-jack'
import Bedinformation from './Bedinfo'
import { DatePicker } from 'antd';
import moment from 'moment';
import { yyyymmddformat } from '../Function/Timeformat'
import './WardBedinformation.scss'

class Wardbedinformation extends Component {
    state = {
        subselectdate: null,
        selectid: null,
        selectDate: new Date().getTime(),
        bedinfo: bedInfo
    }
    onChangeDate = (date, datestring) => {
        if (datestring === '') {
            this.setState(
                {
                    selectDate: new Date().getTime(),
                    selectid: null
                }
            )
        }
        else {
            this.setState(
                {
                    selectDate: new Date(datestring).getTime(),
                    selectid: null
                }
            )
        }
    }
    callbackFunction = (idnumber) => {
        this.setState({
            selectid: idnumber.idkey,
            subselectdate: idnumber.time
        })
    }

    bedInfoData() {

    }
    transferListlayout(trans) {
        var translist = []
        for (let index = 0; index < trans.length; index++) {
            const element = trans[index];
            translist.push(
                <div className='wardbedinfoitemcontextgridRow'>
                    <t2 className='wardbedinfoitemcontextgridRowStyle'>{element.HISID}</t2>
                    <t2 className='wardbedinfoitemcontextgridRowStyle'>{element.name}</t2>
                </div>
            )
        }
        return translist
    }
    infoitem(bedinfo) {
        const selectDateitem = this.state.subselectdate
        const filterdate = bedinfo.filter(function (info, index) {
            return yyyymmddformat(info.date * 1000) === yyyymmddformat(selectDateitem)
        })
        if (this.state.selectid === null) {
            return null
        }
        else {
            if (filterdate.length === 0) {
                //null
                return (
                    <div>
                        <div className='wardbedinfoitemtriangle'>
                            <div className='triangle'></div>
                        </div>
                        <div className='wardbedinfoitemcontext'>
                            <div className='wardbedinfoitemcontextgrid'>
                                <div>
                                    <t2 className='wardbedinfoitemcontexttitle'>轉入病人清單</t2>
                                    <div className='wardbedinfoitemcontextgridlayout'>
                                        <div className='wardbedinfoitemcontextgridRow'>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>病歷號碼</t2>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>姓名</t2>
                                        </div>
                                    </div>
                                </div>
                                <div>
                                    <t2 className='wardbedinfoitemcontexttitle'>轉出病人清單</t2>
                                    <div className='wardbedinfoitemcontextgridlayout'>
                                        <div className='wardbedinfoitemcontextgridRow'>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>病歷號碼</t2>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>姓名</t2>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                )
            }
            else {
                const transIn = filterdate[0].transferInList
                const transOut = filterdate[0].transferOutList
                return (
                    <div>
                        <div className='wardbedinfoitemtriangle'>
                            <div className='triangle'></div>
                        </div>
                        <div className='wardbedinfoitemcontext'>
                            <div className='wardbedinfoitemcontextgrid'>
                                <div>
                                    <t2 className='wardbedinfoitemcontexttitle'>轉入病人清單</t2>
                                    <div className='wardbedinfoitemcontextgridlayout'>
                                        <div className='wardbedinfoitemcontextgridRow'>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>病歷號碼</t2>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>姓名</t2>
                                        </div>
                                        {this.transferListlayout(transIn)}
                                    </div>
                                </div>
                                <div>
                                    <t2 className='wardbedinfoitemcontexttitle'>轉出病人清單</t2>
                                    <div className='wardbedinfoitemcontextgridlayout'>
                                        <div className='wardbedinfoitemcontextgridRow'>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>病歷號碼</t2>
                                            <t2 className='wardbedinfoitemcontextgridRowStyle'>姓名</t2>
                                        </div>
                                        {this.transferListlayout(transOut)}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                )
            }
        }

    }
    bedinfotitle() {
        const maparray = ['天數', '佔床數', '轉出人數', '轉入人數', '入侵式呼吸器', '非入侵式呼吸器', 'TPN人數']
        const title = maparray.map(function (info, index) {
            return (
                <div className='wardbedinfotablerowtitle'>
                    <t2>{info}</t2>
                </div>
            );
        })
        return title
    }
    weekbedinfo(infolistarray) {
        var infolist = []
        for (let i = 0; i < 7; i++) {
            const dateTime = this.state.selectDate
            const localtime = dateTime - (i * 24 * 60 * 60 * 1000);
            const filterdate = infolistarray.filter(function (info, index) {
                return yyyymmddformat(info.date * 1000) === yyyymmddformat(localtime)
            })
            if (filterdate.length === 0) {
                infolist.push(
                    <Bedinformation data={null} key={i} localtime={localtime} selectkey={this.state.selectid} idkey={i} parentCallback={this.callbackFunction}></Bedinformation>
                )
            }
            else {
                infolist.push(
                    <Bedinformation data={filterdate[0]} key={i} localtime={localtime} selectkey={this.state.selectid} idkey={i} parentCallback={this.callbackFunction}></Bedinformation>
                )
            }
        }
        infolist.reverse()
        return infolist

    }
    render() {
        function disabledDate(current) {
            // Can not select days before today and today
            return current && current > moment().endOf('day');
        }
        const dateFormat = 'YYYY/MM/DD';
        return (
            <div style={{ marginTop: "10px" }}>
                <div className='wardbrdinfomationtitle'>
                    <centerstyle>
                        <t2>
                            床位資訊
                        </t2>
                    </centerstyle>
                    <DatePicker disabledDate={disabledDate} defaultValue={moment(yyyymmddformat(this.state.selectDate), dateFormat)} format={dateFormat} onChange={this.onChangeDate} allowClear={false} />
                </div>
                <div className='wardbedinformationcontext'>
                    <div>
                        {this.bedinfotitle()}
                    </div>
                    {this.weekbedinfo(this.state.bedinfo)}
                </div>
                {this.infoitem(this.state.bedinfo)}
            </div>
        );
    }
}

export default Wardbedinformation;