import React, { Component } from 'react';
import * as d3 from "d3";
import '../CrossSectionView/Crosssectionvitalsignchart.scss'
import { yyyymmddformat, hhmmssformattohalf } from '../../Function/Timeformat'

class Chronicvitalchart extends Component {
    state = {
        change: false
    }
    componentDidUpdate() {

        const { svg_id } = this.props
        d3.select("#" + svg_id).remove()
        this.drawChart()
    }

    componentDidMount() {
        this.drawChart();
    }
    drawChart() {
        function intervaldays(select_date, select_interval) {
            switch (select_interval) {
                //一天
                case 0:
                    return select_date - (24 * 60 * 60 * 1000) * 1
                //三天
                case 1:
                    return select_date - (24 * 60 * 60 * 1000) * 3
                //一週
                case 2:
                    return select_date - (24 * 60 * 60 * 1000) * 7
                //兩週
                case 3:
                    return select_date - (24 * 60 * 60 * 1000) * 14
                default:
                    return null
            }
        }

        const { id_key, svg_id, circlrcolor, max, min, select_interval, select_date, datasource, statussource } = this.props
        const viewwidth = window.screen.availWidth - 220, height = 50, viewhieght = height + 10, rect_width = viewwidth / 46
        //選取時間(0點)
        const select_date_zero = yyyymmddformat(select_date)
        //選取時間(7點)
        const End_time_format = new Date(select_date_zero).getTime() + (31 * 60 * 60 * 1000)
        //select_intercal:0一天 1三天 2兩週
        const Start_time = intervaldays(End_time_format, select_interval)

        var y = d3.scaleLinear()
            .domain([min, max])
            .range([height, 0])
        console.log(Start_time, End_time_format)
        var x = d3.scaleLinear()
            .domain([Start_time, End_time_format])
            .range([0, viewwidth - 1])

        console.log(viewhieght, viewwidth)

        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_id)
            .attr("viewbox", [0, 0, viewwidth + 60, 110])
            .style("height", "100px")
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 5 + ")");

        var yAxis = d3.axisLeft(y)
            .tickValues([min, max])
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");

        svg.append('line')
            .attr('x1', x(Start_time))
            .attr('y1', y(max))
            .attr('x2', x(End_time_format))
            .attr('y2', y(max))
            .style('stroke', 'rgba(220,220,220,1)')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', x(Start_time))
            .attr('y1', y(min))
            .attr('x2', x(End_time_format))
            .attr('y2', y(min))
            .style('stroke', 'rgba(220,220,220,1)')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        //一天

        function drawshadow(data, number) {
            console.log(data)
            for (let index = 0; index < data.length; index++) {
                const borderwidth = viewwidth / (number * 6)
                const element = data[index];
                svg.append('line')
                    .attr('x1', x(element.time))
                    .attr('y1', y(20))
                    .attr('x2', x(element.time))
                    .attr('y2', y(120))
                    .style('stroke', circlrcolor)
                    .style('opacity', 0.2)
                    // .style('stroke', 'red')
                    .style('stroke-width', borderwidth)


            }
        }
        if (select_interval === 0) {
            for (let index = 0; index < 25; index++) {
                if (index % 8 === 0) {
                    svg.append('line')
                        .attr('x1', x(Start_time + (index * 60 * 60 * 1000)))
                        .attr('y1', 0)
                        .attr('x2', x(Start_time + (index * 60 * 60 * 1000)))
                        .attr('y2', height)
                        .style('stroke', 'black')
                        .style('stroke-width', 1);
                }
                else {
                    svg.append('line')
                        .attr('x1', x(Start_time + (index * 60 * 60 * 1000)))
                        .attr('y1', 0)
                        .attr('x2', x(Start_time + (index * 60 * 60 * 1000)))
                        .attr('y2', height)
                        .style('stroke', 'rgba(220,220,220,1)')
                        .style('stroke-width', 1)
                        .style('stroke-dasharray', 5.5);
                }
            }
            if (datasource !== null && datasource !== undefined) {
                if (statussource === 'CM') {
                    drawshadow(datasource, 24)
                    for (let index = 0; index < datasource.length; index++) {
                        const element = datasource[index];
                        const eledata = element.data
                        const boxwidth = 5
                        const data_sorted = eledata.sort(d3.ascending)
                        const q1 = d3.quantile(data_sorted, .25)
                        // const median = d3.quantile(data_sorted, .5)
                        const q3 = d3.quantile(data_sorted, .75)
                        const interQuantileRange = q3 - q1
                        const minbox = q1 - 1.5 * interQuantileRange
                        const maxbox = q1 + 1.5 * interQuantileRange

                        svg.append("line")
                            .attr("x1", x(element.time))
                            .attr("x2", x(element.time))
                            .attr("y1", y(minbox))
                            .attr("y2", y(maxbox))
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 0.5)

                        svg.append("rect")
                            .attr("x", x(element.time) - boxwidth / 2)
                            .attr("y", y(q3))
                            .attr("height", (y(q1) - y(q3)))
                            .attr("width", boxwidth)
                            .attr("stroke", circlrcolor)
                            .style("fill", circlrcolor)
                            .style('stroke-width', 0.5);

                        svg.selectAll("toto")
                            .data([minbox, maxbox])
                            .enter()
                            .append("line")
                            .attr("x1", x(element.time) - boxwidth / 2)
                            .attr("x2", x(element.time) + boxwidth / 2)
                            .attr("y1", function (d) { return (y(d)) })
                            .attr("y2", function (d) { return (y(d)) })
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 0.5);

                    }
                }
                else {
                    console.log(datasource)
                    drawshadow(datasource, 24)
                    for (let index = 0; index < datasource.length; index++) {
                        const element = datasource[index];
                        svg.append('circle')
                            .attr('cx', x(element.time))
                            .attr('cy', y(element.data))
                            .attr('r', 3)
                            .style('fill', circlrcolor)
                    }
                }

            }
            else {

            }
        }

        // 三天
        if (select_interval === 1) {
            for (let index = 0; index < 22; index++) {
                if (index % 2 === 0) {
                    svg.append('line')
                        .attr('x1', x(Start_time + (index * 60 * 60 * 1000 * 4)))
                        .attr('y1', 0)
                        .attr('x2', x(Start_time + (index * 60 * 60 * 1000 * 4)))
                        .attr('y2', height)
                        .style('stroke', 'black')
                        .style('stroke-width', 1);
                }
                else {
                    svg.append('line')
                        .attr('x1', x(Start_time + (index * 60 * 60 * 1000 * 4)))
                        .attr('y1', 0)
                        .attr('x2', x(Start_time + (index * 60 * 60 * 1000 * 4)))
                        .attr('y2', height)
                        .style('stroke', 'rgba(220,220,220,1)')
                        .style('stroke-width', 1)
                        .style('stroke-dasharray', 5.5);
                }
            }
            if (datasource !== null) {
                if (statussource === 'CM') {
                    drawshadow(datasource, 18)
                    for (let index = 0; index < datasource.length; index++) {
                        const element = datasource[index];
                        const eledata = element.data
                        const boxwidth = 10
                        const data_sorted = eledata.sort(d3.ascending)
                        const q1 = d3.quantile(data_sorted, .25)
                        // const median = d3.quantile(data_sorted, .5)
                        const q3 = d3.quantile(data_sorted, .75)
                        const interQuantileRange = q3 - q1
                        const minbox = q1 - 1.5 * interQuantileRange
                        const maxbox = q1 + 1.5 * interQuantileRange

                        svg.append("line")
                            .attr("x1", x(element.time))
                            .attr("x2", x(element.time))
                            .attr("y1", y(minbox))
                            .attr("y2", y(maxbox))
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 1)

                        svg.append("rect")
                            .attr("x", x(element.time) - boxwidth / 2)
                            .attr("y", y(q3))
                            .attr("height", (y(q1) - y(q3)))
                            .attr("width", boxwidth)
                            .attr("stroke", circlrcolor)
                            .style("fill", "rgba(255,255,255,1)")
                            .style('stroke-width', 1);

                        svg.selectAll("toto")
                            .data([minbox, maxbox])
                            .enter()
                            .append("line")
                            .attr("x1", x(element.time) - boxwidth / 2)
                            .attr("x2", x(element.time) + boxwidth / 2)
                            .attr("y1", function (d) { return (y(d)) })
                            .attr("y2", function (d) { return (y(d)) })
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 1);

                    }
                }
                else {
                    console.log(datasource)

                    drawshadow(datasource, 18)
                    for (let index = 0; index < datasource.length; index++) {
                        const element = datasource[index];
                        svg.append('circle')
                            .attr('cx', x(element.time))
                            .attr('cy', y(element.data))
                            .attr('r', 3)
                            .style('fill', circlrcolor)
                    }
                }

            }
            else {

            }
        }

        //一週
        if (select_interval === 2) {
            for (let index = 0; index < 22; index++) {
                svg.append('line')
                    .attr('x1', x(Start_time + (index * 60 * 60 * 1000 * 8)))
                    .attr('y1', 0)
                    .attr('x2', x(Start_time + (index * 60 * 60 * 1000 * 8)))
                    .attr('y2', height)
                    .style('stroke', 'black')
                    .style('stroke-width', 1);
            }
            if (datasource !== null) {
                if (statussource === 'CM') {

                    drawshadow(datasource, 21)
                    for (let index = 0; index < datasource.length; index++) {
                        const element = datasource[index];
                        const eledata = element.data
                        const boxwidth = 10
                        const data_sorted = eledata.sort(d3.ascending)
                        const q1 = d3.quantile(data_sorted, .25)
                        // const median = d3.quantile(data_sorted, .5)
                        const q3 = d3.quantile(data_sorted, .75)
                        const interQuantileRange = q3 - q1
                        const minbox = q1 - 1.5 * interQuantileRange
                        const maxbox = q1 + 1.5 * interQuantileRange

                        svg.append("line")
                            .attr("x1", x(element.time))
                            .attr("x2", x(element.time))
                            .attr("y1", y(minbox))
                            .attr("y2", y(maxbox))
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 1)

                        svg.append("rect")
                            .attr("x", x(element.time) - boxwidth / 2)
                            .attr("y", y(q3))
                            .attr("height", (y(q1) - y(q3)))
                            .attr("width", boxwidth)
                            .attr("stroke", circlrcolor)
                            .style("fill", "rgba(255,255,255,1)")
                            .style('stroke-width', 1);

                        svg.selectAll("toto")
                            .data([minbox, maxbox])
                            .enter()
                            .append("line")
                            .attr("x1", x(element.time) - boxwidth / 2)
                            .attr("x2", x(element.time) + boxwidth / 2)
                            .attr("y1", function (d) { return (y(d)) })
                            .attr("y2", function (d) { return (y(d)) })
                            .attr("stroke", circlrcolor)
                            .style('stroke-width', 1);

                    }
                }
                else {
                    console.log(datasource)

                    drawshadow(datasource, 21)
                    for (let index = 0; index < datasource.length; index++) {
                        const element = datasource[index];
                        svg.append('circle')
                            .attr('cx', x(element.time))
                            .attr('cy', y(element.data))
                            .attr('r', 3)
                            .style('fill', circlrcolor)
                    }
                }

            }
            else {

            }
        }
        //兩週
        if (select_interval === 3) {
            for (let index = 0; index < 15; index++) {
                svg.append('line')
                    .attr('x1', x(Start_time + (index * 60 * 60 * 1000 * 24)))
                    .attr('y1', 0)
                    .attr('x2', x(Start_time + (index * 60 * 60 * 1000 * 24)))
                    .attr('y2', height)
                    .style('stroke', 'black')
                    .style('stroke-width', 1);
            }
        }

        // const data = center_monitor
        // if (vitaldays) {

        // }
        // else {
        //     for (let index = 0; index < data.length; index++) {
        //         const element = data[index];
        //         svg.append('circle')
        //             .attr("id", "the_SVG_ID")
        //             .attr('cx', x(element.time * 1000))
        //             .attr('cy', y(element.Data))
        //             .attr('r', 0.1)
        //             .style('fill', circlrcolor)

        //     }
        // }
    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default Chronicvitalchart;