import React, { Component } from 'react';
import editlogo from '../../Image/svg/Edit.svg'
import { Checkbox, Input, Button } from 'antd';
import './Scheduletodo.scss'
import * as ApiProvider from '../../API/ApiProvider';

const TODO_DONE = "已完成";
const TODO_UNFINISH = "未完成";

class Scheduletodo extends Component {
    state = {
        EditMode: false,
        displayMode: true, //true已完成 flase未完成
        todolist: [],
        userData: this.props.userdata,
        transinno: "4",
        inputContent: undefined
    }
    componentDidMount() {
        //bind function
        this.onInputChange = this.onInputChange.bind(this);

        //query patient notice\
        this.queryPatientTodo();
    }
    queryPatientTodo() {
        ApiProvider.getOneTodo(this.state.transinno, this.state.userData.CASEID)
        .then(data => {
            console.log("queryPatientTodo OK " + JSON.stringify(data));
            this.setState({
                todolist: data.data
            });
        })
        .catch(err => {
            console.log("queryPatientTodo NG " + JSON.stringify(err));
        });
    }
    onInputChange(event) {
        this.setState({inputContent: event.target.value})
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }

    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    onClickEnter() {
        const timeNowStr = new Date().toISOString()
        ApiProvider.createTodo(this.state.userData.CASEID, this.state.inputContent, timeNowStr, this.state.transinno, TODO_UNFINISH)
        .then(data => {
            console.log("createTodo OK " + JSON.stringify(data));
            this.queryPatientTodo();
        })
        .catch(err => {
            console.log("createTodo NG " + JSON.stringify(err));
        });
        this.setState(
            {
                EditMode: false,
                inputContent: undefined
            }
        )
    }
    onClickfinish() {
        console.log("finish")
        this.setState(
            {
                displayMode: true
            }
        )
    }
    onClickunfisish() {
        this.setState(
            {
                displayMode: false
            }
        )
    }

    onChange = (checkedValues) => {
        console.log('checked = ', document.getElementById("finish"));
    }
    onItemCheck = (event) => {
        console.log('onItemCheck ' + JSON.stringify(event.target));

        const itemToUpdate = this.state.todolist.find(item => item.time === event.target.value);
        console.log('find item ' + JSON.stringify(itemToUpdate));

        if((this.state.displayMode === true && event.target.checked === false) || (this.state.displayMode === false && event.target.checked === true)) {
            ApiProvider.createTodo(itemToUpdate.caseid, itemToUpdate.context, itemToUpdate.time, itemToUpdate.transinno, (itemToUpdate.type === TODO_DONE) ? TODO_UNFINISH : TODO_DONE)
            .then(data => {
                console.log("updateTodo OK " + JSON.stringify(data));
                this.queryPatientTodo();
            })
            .catch(err => {
                console.log("updateTodo NG " + JSON.stringify(err));
            });
        }
    }
    render() {
        const button_style = {
            finish: { display: "flex", justifyContent: "center", alignItems: "center", borderRadius: "16px", border: "rgba(245, 166, 35, 1) 1px solid", color: "rgba(245, 166, 35, 1)" },
            unfinish: { display: "flex", justifyContent: "center", alignItems: "center", borderRadius: "16px", border: "rgba(245, 166, 35, 1) 1px solid", background: "rgba(245, 166, 35, 1)", color: "white" }
        }

        const todolist_data = this.state.todolist
        const finish_data = todolist_data.filter(function (item, index, array) {
            return item.type === TODO_DONE;
        });
        const finish_value = finish_data.map(function (item, index, array) {
            return item.time;
        });
        const unfinish_data = todolist_data.filter(function (item, index, array) {
            return item.type === TODO_UNFINISH;
        });

        const onItemCheck = this.onItemCheck;
        const finish_checkbox = finish_data.map(function (item, index, array) {
            return (
                <div key={index} className='scheduletodocell'>
                    <Checkbox value={item.time} onChange={onItemCheck}>{item.context}</Checkbox>
                </div>
            );
        });
        const unfinish_checkbox = unfinish_data.map(function (item, index, array) {
            return (
                <div key={index} className='scheduletodocell'>
                    <Checkbox value={item.time} onChange={onItemCheck}>{item.context}</Checkbox>
                </div>
            );
        });
        return (
            <div style={{ marginBottom: "10px" }}>
                <t1>To Do List</t1>
                <div className='scheduletodotitle'>
                    <t1>待辦排程</t1>
                    <div onClick={() => this.onChangeEditMode()}>
                        <img src={editlogo} alt="editlogo" />
                    </div>
                </div>
                <div style={{ display: "flex", justifyContent: "center", alignItems: "center", padding: "10px 0px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "115px 115px", gridColumnGap: "10px" }}>
                        <div className={this.state.displayMode ? 'finishbutton' : 'todobutton'} onClick={() => this.onClickunfisish()}>未完成</div>
                        <div onClick={() => this.onClickfinish()} className={this.state.displayMode ? 'todobutton' : 'finishbutton'}>已完成</div>
                    </div>
                </div>
                <Checkbox.Group defaultValue={finish_value} style={{ width: '100%' }} onChange={this.onChange}>
                    <div style={{ overflowY: "auto", maxHeight: "200px" }}>
                        {this.state.displayMode ? finish_checkbox : unfinish_checkbox}
                    </div>
                </Checkbox.Group>
                <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                    <div style={{ padding: "5px 10px", display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <Input placeholder="輸入當日註記內容" value={this.state.inputContent} onChange={this.onInputChange}></Input>
                        <Button type="primary">新增</Button>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                            <Button onClick={() => this.onClickCancel()}>取消</Button>
                            <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Scheduletodo