import React, { Component } from 'react';
import * as d3 from "d3";
import { growdata } from '../jsonResponse'
import { wardchartdatasource,wardchartoncustom } from '../Data/Wardviewdata'

class Wardmonthafter extends Component {
    componentDidMount() {
        this.drawChart();
    }

    drawChart() {
        const { gender, svg_key } = this.props
        const width = (window.screen.availWidth - 50) / 2 - 50, height = 400

        //var vertigo = datarelease();

        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([1000, 10000])
            .range([height, 0])

        var x = d3.scaleLinear()
            .domain([0, 6])
            .range([0, width])

        var yAxis = d3.axisLeft(y)
            .ticks(10)
            .tickSize(0, 0)
            .tickPadding(10);


        var xAxis = d3.axisTop(x)
            .ticks(6)
            .tickSize(0, 0)
            .tickPadding(-15)
            .tickFormat(function (d) {
                if (d === 0) {
                    return "birth"
                }
                else return 'M' + d;
            });


        var svg = d3
            .select("#" + svg_key)
            // .style("height", "300px")
            // .style("width", svgwidth + "px")
            // .select("svg")
            .attr("viewBox", [0, 0, width + 80, height + 50])
            .append("g")
            .attr("transform", "translate(" + 50 + "," + 20 + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");



        svg.append("g")
            .call(xAxis)
            .attr("class", "axis")
            .attr("transform", "translate(" + 0 + ", " + height + ")");

        for (let index = 0; index < 7; index++) {
            svg.append('line')
                .attr('x1', x(index))
                .attr('y1', 0)
                .attr('x2', x(index))
                .attr('y2', height)
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);

        }

        for (let index = 0; index < 19; index++) {
            const number = index * 500 + 1000
            svg.append('line')
                .attr('x1', 0)
                .attr('y1', y(number))
                .attr('x2', width)
                .attr('y2', y(number))
                .style('stroke', 'rgba(187, 187, 187, 1)')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);

        }

        // for (let index = 22; index < 45; index++) {
        //     svg.append('line')
        //         .attr('x1', x(index))
        //         .attr('y1', 0)
        //         .attr('x2', x(index))
        //         .attr('y2', height)
        //         .style('stroke', 'rgba(187, 187, 187, 1)')
        //         .style('stroke-width', 1)
        //         .style('stroke-dasharray', 5.5);
        // }

        if (gender === 'Male') {
            for (let index = 0; index < growdata.Male_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.Male_after[index].y))
                    .attr('y1', y(growdata.Male_after[index].min))
                    .attr('x2', x(growdata.Male_after[index + 1].y))
                    .attr('y2', y(growdata.Male_after[index + 1].min))
                    .style('stroke', 'orange')
                    .style('stroke-width', 1.5);
            }

            for (let index = 0; index < growdata.Male_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.Male_after[index].y))
                    .attr('y1', y(growdata.Male_after[index].max))
                    .attr('x2', x(growdata.Male_after[index + 1].y))
                    .attr('y2', y(growdata.Male_after[index + 1].max))
                    .style('stroke', 'blue')
                    .style('stroke-width', 1.5);
            }
        }
        else {
            for (let index = 0; index < growdata.FeMale_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.FeMale_after[index].y))
                    .attr('y1', y(growdata.FeMale_after[index].min))
                    .attr('x2', x(growdata.FeMale_after[index + 1].y))
                    .attr('y2', y(growdata.FeMale_after[index + 1].min))
                    .style('stroke', 'orange')
                    .style('stroke-width', 1.5);
            }

            for (let index = 0; index < growdata.FeMale_after.length - 1; index++) {
                svg.append('line')
                    .attr('x1', x(growdata.FeMale_after[index].y))
                    .attr('y1', y(growdata.FeMale_after[index].max))
                    .attr('x2', x(growdata.FeMale_after[index + 1].y))
                    .attr('y2', y(growdata.FeMale_after[index + 1].max))
                    .style('stroke', 'blue')
                    .style('stroke-width', 1.5);
            }
        }

        const datasource = wardchartdatasource
        for (let index = 0; index < datasource.length; index++) {
            const element = datasource[index];
            svg.append('circle')
                .attr('cx', x(element.x))
                .attr('cy', y(element.y))
                .attr('r', 3)
                .style('fill', 'green')
        }
        const datacustom = wardchartoncustom
        for (let index = 0; index < datacustom.length; index++) {
            const element = datacustom[index];
            svg.append('circle')
                .attr('cx', x(element.x))
                .attr('cy', y(element.y))
                .attr('r', 3)
                .style('fill', 'blue')
        }

    }

    render() {
        const { id_key, svg_key } = this.props
        return (
            <div id={id_key}>
                <svg id={svg_key}></svg>
            </div>
        );
    }
}

export default Wardmonthafter;
