import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//Component
import Wardcardvalue from '../Wardcardvalue'
import { bednumberformat, gender, pregancyweeksanddays } from '../../Function/Wardbedfunction'
import { datestringformat, yyyymmddHHmmfromat, apiyyyymmddformat } from '../../Function/Timeformat'
import './Wardcarddetail.scss'
//image
import editlogo from '../../Image/svg/edit2.svg'
import fileLogo from '../../Image/svg/file1.svg'
import file2Logo from '../../Image/svg/file2.svg'
//antdesign
import { Modal, Input, DatePicker, Button } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider';
import X2JS from 'x2js';


class Wardcarddetail extends Component {
    state = {
        hoverstate: "none",
        clickstate: false,
        mouse_hover: false,
        edit_hover: false,
        visible: false,
        pbasignfo: null,
        birthdayweight: "N",
        weight: 0,
        bornweek: 0,
        bornday: 0,
        nowweight: 0,
        report: 0,
        nonreport: 0,
        remark: "",
        TRANSINID:"",
        TRANSINDT:"",
        TRANSINTM:""
    };

    componentDidMount() {
        const xm2 = new X2JS()
        //data = ALL HBED
        const { data, bednumber } = this.props
        const datafiliter = data.filter(function (item, index, array) {
            console.log(parseInt(item.HBEDNO), bednumber)
            return parseInt(item.HBEDNO) === bednumber
        })
        if (datafiliter.length === 0) console.log("error" + datafiliter)
        else {
            const source = datafiliter[0]
            const caseid = source.PCASENO
            const hisid = source.PHISTNUM
            ApiProvider.getTablePBASINFO(hisid)
                .then(data => {
                    this.setState({
                        pbasignfo: data.data
                    })
                    const PBASINGNINFO = data.data
                    const starttime = yyyymmddHHmmfromat(new Date(datestringformat(PBASINGNINFO.PBIRTHDT)))
                    const endtime = yyyymmddHHmmfromat(new Date())
                    ApiProvider.getQTWPER(caseid, starttime, endtime)
                        .then(data => {
                            const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                            var weightlist = []
                            for (let index = 0; index < parsedata.length; index++) {
                                const element = parsedata[index];
                                if (element.type === 'TW') {
                                    if (element.rec_dat === PBASINGNINFO.PBIRTHDT) {
                                        this.setState({
                                            birthdayweight: Number(element.value2) * 1000
                                        })
                                    }
                                    weightlist.push(Number(element.value2))
                                }
                            }
                            this.setState({
                                weight: weightlist[0] * 1000
                            })

                        })
                        .catch(err => {
                            console.log("Andy test getQTWPER NG " + JSON.stringify(err));
                        });
                })
                .catch(err => {
                    console.log("Andy test getTableHBED NG " + JSON.stringify(err));
                });

            ApiProvider.getQMNC(caseid)
                .then(data => {
                    console.log(xm2.xml2js(data.data))
                    const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                    this.setState({
                        bornweek: parsedata.weeks,
                        bornday: parsedata.days,
                    })
                })
                .catch(err => {
                    console.log("Andy test getQMNC NG " + JSON.stringify(err));
                });

            ApiProvider.getStoredProcedureRESSECT(hisid, 1, "ALL", "DOC3924B")
                .then(data => {
                    this.setState({
                        report: data.data.length
                    })
                })
                .catch(err => {
                    console.log("Andy test getTableHBED NG " + JSON.stringify(err));
                });
            ApiProvider.getStoredProcedureORDLIST(hisid, apiyyyymmddformat((new Date().getTime() - (24 * 60 * 60 * 1000))), "ALL", "DOC3924B")
                .then(data => {
                    function datasize(datalist) {
                        const list = []
                        for (let index = 0; index < datalist.length; index++) {
                            const element = datalist[index];
                            if (element.ORSTATUS === "31") {
                                list.push(element)
                            }
                        }
                        return list.length
                    }
                    this.setState({
                        nonreport: datasize(data.data)
                    })
                })
                .catch(err => {
                    console.log("Andy test getTableHBED NG " + JSON.stringify(err));
                });
            ApiProvider.getTablePLOC(caseid)
                .then(data => {
                    const matadata = data.data
                    const i = matadata.length
                    const d = matadata[i - 1]
                    const transinid = "NICU" + d.PLOCDT + d.PLOCTM
                    const transintime = d.PLOCTM
                    const transindt = d.PLOCDT
                    this.setState({
                        TRANSINID:transinid,
                        TRANSINDT:transindt,
                        TRANSINTM:transintime
                    })
                })
                .catch(err => {
                    console.log("Andy test getTableHBED NG " + JSON.stringify(err));
                });

        }
    }
    sendData = (HISID) => {
        const { previewmode } = this.props
        if (previewmode) {
            this.props.parentCallback(HISID);
        }
    }

    //Module show
    handleOk(string) {
        this.setState({
            visible: false
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
        });
    };
    showModal = () => {
        this.setState({
            visible: true
        });
    };
    ondatechange = (date, dateString) => {
        console.log(dateString)
    }

    Modelrender(source) {
        function typefrom(from) {
            if (from === 'Custom') {
                return true
            }
            return false
        }
        const pbasignfodata = this.state.pbasignfo
        if (pbasignfodata === null) {
            return null
        }
        return (
            <Modal
                width={270}
                title="填寫病床資訊"
                visible={this.state.visible}
                onCancel={this.handleCancel}
                footer={
                    <div style={{ display: "flex", justifyContent: typefrom(source.type) ? 'space-between' : 'flex-end', alignItems: "center" }}>
                        <Button key="delete" style={typefrom(source.type) ? { border: "1px solid rgba(208,2,27,1)", color: "rgba(208,2,27,1)" } : { display: "none" }} onClick={this.handleCancel}>清床</Button>
                        <div>
                            <Button key="cancel" onClick={this.handleCancel}>取消</Button>
                            <Button key="ok" type="primary" onClick={this.handleOk}>儲存</Button>
                        </div>
                    </div>}>
                <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr 1fr 1fr", gridRowGap: "10px" }}>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>病床號碼:</t2>
                        </centerstyle>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <div>{bednumberformat(parseInt(source.HBEDNO))}</div>
                        </div>
                    </div>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>病歷號碼:</t2>
                        </centerstyle>
                        <t2>{source.PHISTNUM}</t2>
                    </div>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>出生週數:</t2>
                        </centerstyle>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <div style={{ marginRight: "20px" }}>{this.state.bornweek}</div>
                            <t2 style={{ marginRight: "20px" }}>週</t2>
                            <div style={{ marginRight: "20px" }}>{this.state.bornday}</div>
                            <t2>日</t2>
                        </div>
                    </div>
                    <div className='modulecontent'>
                        <div style={{ display: 'flex', justifyContent: "space-between", alignItems: "cneter", width: "69px" }}>
                            <t2>生</t2>
                            <t2>日:</t2>
                        </div>
                        <t2>{datestringformat(pbasignfodata.PBIRTHDT)}</t2>
                    </div>
                    <div className='modulecontent'>
                        <centerstyle>
                            <t2>出生體重:</t2>
                        </centerstyle>
                        <div style={{ display: 'flex' }}>
                            <Input placeholder="1200" id="weight" defaultValue={this.state.birthdayweight} />&nbsp;g
                        </div>
                    </div>
                </div>
            </Modal>
        );
    }
    render() {
        function modedisplay(mode) {
            if (mode) {
                return "none"
            }
            else {
                return "block"
            }
        }
        function modecursor(mode) {
            if (mode) {
                return "pointer"
            }
            else {
                return "default"
            }
        }
        function selectstyle(selectstate, HISID) {
            if (selectstate === HISID) {
                return 'wardcarddetailborderclick'
            }
            else {
                return 'wardcarddetailborderunclick'
            }
        }
        const { data, selectstate, simplemode, previewmode, bednumber, datalist } = this.props
        const datafiliter = data.filter(function (item) {
            return parseInt(item.HBEDNO) === bednumber
        })
        const source = datafiliter[0]
        if (source === undefined) {
            return null
        }
        const pbasignfodata = this.state.pbasignfo
        if (pbasignfodata === null) {
            return null
        }
        console.log("bornweek =" + this.state.bornweek)
        return (
            <div className={selectstyle(selectstate, source.PHISTNUM)}>
                <div className='wardcarddetailtitle'>
                    <t3 style={{ color: "rgba(61, 119, 181, 1)" }}>{bednumberformat(parseInt(source.HBEDNO))},{source.PNAMEC},{gender(source.PSEX)},{datestringformat(source.PBIRTHDT)},{source.PHISTNUM}</t3>
                    <div onClick={this.showModal} className='wardcarddetileditbutton'>
                        <img src={editlogo} alt='editlogo'></img>
                    </div>
                </div>
                <div style={{ cursor: modecursor(previewmode) }} onClick={() => this.sendData(source.PHISTNUM)}>
                    <div className='wardinfoweeks'>
                        <t2 style={{ color: "rgba(59, 151, 225, 1)" }}>[{this.state.bornweek}+{this.state.bornday}]&rarr;{pregancyweeksanddays(this.state.bornweek, this.state.bornday, datestringformat(source.PBIRTHDT))}</t2>
                        <t4>{this.state.birthdayweight}g&rarr;{this.state.weight}g</t4>
                        <div className='wardinforeportgroup'>
                            <img src={fileLogo} alt='fileLogo'></img>
                            <t4>{this.state.report}</t4>
                            <img src={file2Logo} alt='file2Logo'></img>
                            <t4>{this.state.nonreport}</t4>
                        </div>
                    </div>
                    <div style={{ display: modedisplay(simplemode), borderBottomWidth: "0.5px", borderBottomColor: "rgba(215, 238, 255, 1)", borderBottomStyle: "solid" }}>
                        <Wardcardvalue source={source}></Wardcardvalue>
                    </div>
                    <div style={{ height: '30px', display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: "0px 10px", position: 'relative' }}>
                        <t4>{this.state.remark}</t4>
                        <Link className='patientviewbutton' to={{ pathname: "/nicu/patient/" + source.PHISTNUM, state: { "datalist": data, "HISID": source.PHISTNUM, "CASEID": source.PCASENO, "BEDNUMBER": source.HBEDNO, "TYPE": true,"TRANSINID":this.state.TRANSINID } }}>
                            <centerstyle>
                                <t4>進入病人資料頁面</t4>
                            </centerstyle>
                        </Link>
                    </div>
                </div>
                {this.Modelrender(source)}
            </div>
        );
    }
}

export default Wardcarddetail;