import React, { Component } from 'react';
import { Labwarningvalue } from '../Function/Wardstate'
class Wardlabviewitem extends Component {
    render() {

        const { data, title } = this.props
        const filterarray = data.filter(function (item, index) {
            return item.item === title
        });
        return (
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "100%" }}>
                <div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>{filterarray[0].item}</div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center", color: Labwarningvalue(filterarray[0].value, filterarray[0].item) ? "black" : "red" }}>{filterarray[0].value}</div>
                </div>
            </div>
        );
    }
}

export default Wardlabviewitem