import React, { Component } from 'react';
import RTbreathetablecell from './RTbreathtablecell'
import { RTbreathelist } from '../../Data/Wardviewdata'
import { yyyymmddformat } from '../../Function/Timeformat'


class RTbreathetable extends Component {
    render() {
        const { PickerDate } = this.props
        const RTbreathelistrowdata = RTbreathelist.filter(function (info, index) {
            return yyyymmddformat(info.time) === yyyymmddformat(PickerDate)
        })
        let RTbreathetable = RTbreathelistrowdata.map(
            (info, index) =>
                <RTbreathetablecell key={index} info={info}></RTbreathetablecell>
        )
        return (
            <div>
                <div style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 3fr 3fr", backgroundColor: "#E2EDF3", border: "rgba(238,238,238,1) 1px solid" }}>
                    <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>紀錄日期</t3>
                    <t3 style={{ display: "flex", alignItems: "center", padding: "0px 5px" }}>Mode</t3>
                    <t3 style={{ display: "flex", alignItems: "center", padding: "0px 5px" }}>Setting</t3>
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {RTbreathetable}
                </div>
            </div>
        );
    }
}

export default RTbreathetable