import React, { Component } from 'react';
import { MMDDhhmmfromat,yyyymmddformat } from '../../Function/Timeformat'
import { gasdata } from '../../Data/Wardviewdata'


class RTtable extends Component {
    render() {
        const rttitle = ['起迄', 'PH', 'PCO2', 'PO2', 'HCO3', 'BE', 'O2', 'Sample', 'SaCO2']
        const { PickerDate } = this.props
        function tableformat(itemarray) {
            const result = itemarray.map(function (info, index) {
                return (
                    <centerstyle key={index}>
                        <t3>{info}</t3>
                    </centerstyle>
                );
            })
            return result
        }
        function tablecelldata(data) {
            return (
                <div style={{ height: "50px", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{MMDDhhmmfromat(data.time)}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.PH}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.PCO2}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.PO2}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.HCO3}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.BE}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.O2}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.Sample}</t3>
                    </centerstyle>
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                        <t3>{data.SaCO2}</t3>
                    </centerstyle>
                </div>

            );
        }

        const sortarray = gasdata.filter(function(info,index){
            return yyyymmddformat(info.time) === yyyymmddformat(PickerDate)
        })
        console.log(sortarray)
        return (
            <div>
                <div style={{ height: "50px", border: "rgba(238,238,238,1) 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>
                    {tableformat(rttitle)}
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {sortarray.map(function (info, index) {
                        return tablecelldata(info)
                    })}
                </div>
            </div>
        );
    }
}

export default RTtable