import React, { Component } from 'react'
import { gasdata, RTbreathelist } from '../../Data/Wardviewdata'

class Rttable extends Component {

    TableStyle(gridstate) {
        function breath() {
            const list = RTbreathelist.map(function (info, index) {
                return (
                    <div style={Breath_title_style.context_layout} key={index}>
                        <div style={Breath_title_style.context_1}>{info.time}</div>
                        <div style={Breath_title_style.context_2}>{info.Mode}</div>
                        <div style={Breath_title_style.context_2}>{info.Setting}</div>
                    </div>
                );
            })
            return list
        }
        function gascell() {
            const list = gasdata.map(function (info, index) {
                return (
                    <div key={index} style={{ display: 'grid', gridTemplateColumns: "110px repeat(8,1fr)", height: '50px', border: "rgba(232,232,232,1) 1px solid" }}>
                        <centerstyle>
                            <t2>{info.time}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.PH}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.PCO2}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.PO2}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.HCO3}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.BE}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.O2}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.Sample}</t2>
                        </centerstyle>
                        <centerstyle>
                            <t2>{info.SaCO2}</t2>
                        </centerstyle>
                    </div>
                );
            })
            return list
        }
        const gas_title_style = {
            title: { display: "flex", alignItems: "center", justifyContent: "center", fontSize: "16px" },
            title_context: { display: "grid", gridTemplateColumns: "repeat(8,1fr)" },
            context_1: {height:'50px', display: "flex", alignItems: "center", justifyContent: "center", fontSize: "16px", border: "rgba(238,238,238,1)" },
            context_2: {height:'50px', display: "flex", alignItems: "center", justifyContent: "center", fontSize: "16px", border: "rgba(238,238,238,1)", borderLeftWidth: "0px" }
        }
        const gas_table_title = [{ title: "PH" }, { title: "PCO2" }, { title: "PO2" }, { title: "HCO3" }, { title: "BE" }, { title: "O2" }, { title: "Sample" }, { title: "SaCO2" }]
        const gas_table_cell = gas_table_title.map((info, index) => <div key={index} style={gas_title_style.title}>{info.title}</div>)
        const gas = (
            <div style={{ marginTop: "10px" }}>
                <div style={{ backgroundColor: "#e2edf3", display: 'grid', gridTemplateColumns: "2fr 8fr", height: '50px' }}>
                    <div style={gas_title_style.title}>記錄日期</div>
                    <div style={gas_title_style.title_context}>{gas_table_cell}</div>
                </div>
                <div style={{ maxHeight: "60vh", overflow: "auto" }}>
                    {/* GAS內容 */}
                    {gascell()}
                </div>
            </div>
        )

        const Breath_title_style = {
            title_1: { display: "flex", alignItems: "center", justifyContent: "center", fontSize: "16px" },
            title_2: { display: "flex", alignItems: "center", fontSize: "16px", padding: "0px 10px" },
            context_layout: { display: 'grid', gridTemplateColumns: "2fr 2fr 4fr" },
            context_1: {height:"50px", display: "flex", justifyContent: "center", alignItems: "center", fontSize: "16px", border: "rgba(238,238,238,1) 1px solid", padding: "0px 10px" },
            context_2: {height:"50px", display: "flex", alignItems: "center", fontSize: "16px", border: "rgba(238,238,238,1) 1px solid", borderLeftWidth: "0px", padding: "0px 10px" }
        }
        const Breath = (
            <div style={{ marginTop: "10px" }}>
                <div style={{ backgroundColor: "#e2edf3", display: 'grid', gridTemplateColumns: "2fr 2fr 4fr", height: '50px' }}>
                    <div style={Breath_title_style.title_1}>時間</div>
                    <div style={Breath_title_style.title_2}>Mode</div>
                    <div style={Breath_title_style.title_2}>Setting</div>
                </div>
                <div style={{ maxHeight: "60vh", overflowY: "auto" }}>
                    {/* 內容 */}
                    {breath()}
                </div>
            </div>
        )
        switch (gridstate) {
            case "Gas":
                return gas
            case "Breath":
                return Breath
            default:
                return null
        }
    }
    render() {
        const { gridcount } = this.props
        const table = this.TableStyle(gridcount)
        return table
    }
}

export default Rttable;