import React, { Component } from 'react'
import rtlogo from '../../Image/svg/RT.svg'
import { rtoneday, rethird } from '../../Data/Wardviewdata'
import { arraychange } from '../../Function/Wardstate'
import { Tooltip } from 'antd'

class Chronicrttable extends Component {
    // 0:一天 1:三天 2:一週 3:兩週
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }

    rendertooltip() {
        return (
            <div>123</div>
        );
    }
    table_content() {
        const { chronic_state } = this.props
        function changetext(text) {
            if (text === 0) {
                return ''
            }
            return <img src={rtlogo} alt="rtlogo" />
        }
        function changgestyle(key) {
            if (key === 0) {
                return { 'data': rtoneday }
            }
            else return { 'data': rethird }
        }
        function changgetiptext(key) {
            if (key === 0) {
                return (
                    <div style={{ backgroundColor: "white", width: "200px", color: 'black' }}>12:30 PM / HFOV
            FiO2: 30%, AMP: 24, MAP:9, Freq:15Hz
            12:10 PM/ HFOV
            FiO2: 35%, AMP: 24, MAP:9, Freq:15Hz</div>
                )
            }
            else return (
                <div style={{ backgroundColor: "white", width: "200px", color: 'black' }}>16:30 PM / HFOV
                FiO2: 30%, AMP: 24, MAP:9, Freq:15Hz
                
                15:10 PM/ HFOV
                FiO2: 35%, AMP: 24, MAP:9, Freq:15Hz</div>
            )
        }
        const table_item = arraychange(changgestyle(chronic_state).data)
        const cell = table_item.map(function (info, index) {
            const tipcontext = changgetiptext(chronic_state)
            if (info === 0) {
                return (
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                        {changetext(info)}
                    </centerstyle>
                );
            }
            else {
                return (
                    <Tooltip placement="right" autoAdjustOverflow={true} overlayStyle={{ background: "white" }} title={tipcontext}>
                        <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                            {changetext(info)}
                        </centerstyle>
                    </Tooltip>
                );
            }
        })
        return cell
    }
    render() {
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", height: "40px" }}>
                <div style={{ backgroundColor: "#cccccc", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>呼吸評估</t2>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>
                    {this.table_content()}
                </div>
            </div>
        );
    }
}

export default Chronicrttable