import React, { Component } from 'react'
import './Infectionlabtable.scss'
import { chronicladdataoneday, chroniclabdatathird } from '../../Data/Wardviewdata'
import { arraychange } from '../../Function/Wardstate'

class Infectionlabtable extends Component {
    switch_state() {
        const chronic_state = 2
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 18
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    } 
    switch_state_title() {
        return "140px"
    }
    table_content() {
        const { chronic_state } = this.props
        function changetext(text) {
            if (text === 0) {
                return ''
            }
            return text
        }
        function changgestyle(key) {
            if (key === 0) {
                return {'data':chronicladdataoneday}
            }
            else return {'data':chroniclabdatathird}
        }
        const table_item = arraychange(changgestyle(chronic_state).data)
        const cell = table_item.map(function (info, index) {
            return (
                <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                    <t2>{changetext(info)}</t2>
                </centerstyle>
            );
        })
        return cell
    }
    render() {
        const { userdata, title } = this.props
        const title_list = title.map((info, index) => <div className='Chronictabletitlelist' key={index}><t2>{info.title}</t2></div>)
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                    {title_list}
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                    {this.table_content()}
                </div>
            </div>
        );
    }
}

export default Infectionlabtable