function statecolor(state) {
    if (state === 'HR') {
        return "#779F50"
    }
    if (state === 'BP') {
        return "#C12528"
    }

    if (state === 'ABP') {
        return "#C12528"
    }
    if (state === 'NBP') {
        return "#C12528"
    }
    if (state === 'SpO2') {
        return "#4773B8"
    }
    if (state === 'BT') {
        return "#7F3DB5"
    }
    if (state === 'RR') {
        return "#DE7531"
    }
    return null
}
function Respiratorswitch(Respirator) {
    if (Respirator === 'non-invasive') {
        return '呼(非侵入)'
    }
    if (Respirator === 'false') {
        return '呼吸'
    }
    return '呼(侵入)'
}

function wardstatestringformat(item) {
    //console.log(item);
    switch (item.item) {
        case "Respirator":
            return Respiratorswitch(item.data)
        case "TPN":
            return "TPN"
        case "NPO":
            return "NPO"
        case "HD":
            return "HD"
        case "Anti":
            return "Anti"
        case "Foley":
            return "Foley"
        case "Drain":
            return "Drain"
        default:
            return item
    }
}

function Labwarningvalue(value, title) {
    if (title === "Glucose") {
        if (value > 120 || value < 50) {
            return false
        }
    }
    if (title === "WBC") {
        if (value < 10000 || value > 30000) {
            return false
        }
    }
    if (title === "Hgb") {
        if (value < 10 || value > 18) {
            return false
        }
    }
    if (title === "Platelet") {
        if (value < 100000 || value > 450000) {
            return false
        }
    }
    if (title === "Na+") {
        if (value < 135 || value > 145) {
            return false
        }
    }
    if (title === "K+") {
        if (value < 3.5 || value > 5.5) {
            return false
        }
    }
    if (title === "Ca2+") {
        if (value < 0.8 || value > 1.4) {
            return false
        }
    }
    if (title === "P") {
        if (value < 4.5 || value > 6.0) {
            return false
        }
    }
    if (title === "Mg2+") {
        if (value < -1.5 || value > 4.5) {
            return false
        }
    }
    if (title === "Alb") {
        if (value > 3) {
            return false
        }
    }
    if (title === "CRP") {
        if (value < 1) {
            return false
        }
    }
    if (title === "Procalcitionin") {
        if (value < 0.5) {
            return false
        }
    }
    if (title === "pH") {
        if (value < 7.25 || value > 7.4) {
            return false
        }
    }
    if (title === "PCO2") {
        if (value < 40 || value > 60) {
            return false
        }
    }
    if (title === "BE") {
        if (value < -7 || value > 6) {
            return false
        }
    }
    if (title === "PO2") {
        if (value < 40 || value > 90) {
            return false
        }
    }
    if (title === "BUN") {
        if (value < 1) {
            return false
        }
    }
    if (title === "Cr") {
        if (value < 1) {
            return false
        }
    }
    if (title === "ALT") {
        if (value < 45) {
            return false
        }
    }
    if (title === "AST") {
        if (value < 45) {
            return false
        }
    }
    if (title === "TBil") {
        if (value < 2) {
            return false
        }
    }
    if (title === "DBil") {
        if (value < 0.5) {
            return false
        }
    }
    if (title === "ALKP") {
        if (value < 450) {
            return false
        }
    }
    if (title === "Total Ca") {
        if (value < 8 || value > 12) {
            return false
        }
    }
    if (title === "FeSat") {
        if (value < 0.15) {
            return false
        }
    }
    return true
}

function simulationCentermonitordata(time, timeInterval) {
    var dataarray = [];
    for (var k = 0; k < (timeInterval * 6); k++) {
        var dataset = []; //建立空的資料陣列
        for (var i = 0; i < timeInterval; i++) {
            var newNum = Math.floor(Math.random() * 50) + 50;
            dataset.push(newNum);
        }
        dataarray.push({
            'data': dataset,
            'time': time[k]
        });
    }
    //console.log(dataarray);
    return dataarray;
}

function simulationnisdata(time, timeInterval) {
    var dataarray = [];
    for (var k = 0; k < timeInterval; k++) {
        var dataset = []; //建立空的資料陣列
        var newNum = Math.floor(Math.random() * 50) + 50;
        dataset.push(newNum);
        dataarray.push({
            'data': dataset,
            'time': time[k]
        });
    }
    //console.log(dataarray);
    return dataarray;
}

function getRandom(number) {
    return Math.floor(Math.random() * 5) + number;
};

function getRandomfornumber(number) {
    return Math.floor(Math.random() * number);
};

function arraychange(list) {
    var myNewArray = []
    for (var i = 0; i < list.length; ++i) {
        for (var j = 0; j < list[i].length; ++j) {
            myNewArray.push(list[i][j]);
        }
    }
    return myNewArray
}

