import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import { Modal, Input } from 'antd'
import './RTbreathtablecell.scss'
import { mmddformat, hhmmssformat } from '../../Function/Timeformat'

class RTbreathetablecell extends Component {
    state = {
        onCell: false,
        onEdit: false
    }

    Mouse_move() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: true
                }
            )
        }
    }

    Mouse_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: false
                }
            )
        }
    }

    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    render() {
        const { info } = this.props
        const { TextArea } = Input;
        return (
            <div>
                <Modal
                    title="備註"
                    visible={this.state.onEdit}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <div style={{ display: "grid", gridTemplateRows: "30px 30px 30px 30px" }}>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <div>開立時間:</div>
                            <div>上次編輯時間:</div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <div>處理名稱:</div>
                            <div>上次編輯人:</div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <div>頻次:</div>
                            <div>開立醫師:</div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>備註內容:</div>
                        <TextArea rows={5} />
                    </div>
                </Modal>
                <div onMouseMove={() => this.Mouse_move()} onMouseLeave={() => this.Mouse_leave()} className="RTtablecell">
                    <t3 style={{ display: "flex", alignItems: "center", justifyContent: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>{mmddformat(info.time)} {hhmmssformat(info.time)}</t3>
                    <div style={{ display: "grid", gridTemplateRows: "50px 50px" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr" }}>
                            <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{info.Mode}</t3>
                            <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{info.Setting}</t3>
                        </div>
                        <t3 style={{ fontSize: "1rem", display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{info.Note}</t3>
                    </div>
                    <div style={this.state.onCell ? { display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: "130px", background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default RTbreathetablecell