import React, { Component } from 'react';
import './ExamNonlabtablecell.scss'
// import { Nonlablist } from '../../Data/Wardviewdata'
import { Nonlablist } from '../../Data/Wardviewdata-jack'

class Examnonlabviewtable extends Component {
    render() {
        function statusflag(status) {
            switch (status) {
                case 0:
                    return '已開單'
                case 1:
                    return '已簽收'
                case 2:
                    return '已發報告'
                default:
                    return '--'
            }
        }
        let labtable = Nonlablist.map(
            (info, index) =>
                <div key={index} style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                    <div className='examnontablecell'>{info.time}</div>
                    <div className='examnontablecell'>{info.item}</div>
                    <div className='examnontablecell'>{statusflag(info.status)}</div>
                    <div className='examnontablecell'>{info.result}</div>
                </div>
        )

        return (
            <div style={{marginTop:"10px"}}>
                <div style={{ height: "50px", border: "#e2edf3 1px solid", backgroundColor: "#e2edf3", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                    <div style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                        <t3>檢驗日期</t3>
                    </div>
                    <div style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                        <t3>檢驗項目名稱</t3>
                    </div>
                    <div style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                        <t3>檢驗項目狀態</t3>
                    </div>
                    <div style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                        <t3>檢驗結果</t3>
                    </div>
                </div>
                <div className='examnonlabviewtable'>
                    {labtable}
                </div>
            </div>
        );
    }
}

export default Examnonlabviewtable