import React, { Component } from 'react';
import { Select } from 'antd';
import Chronicvitalchart from '../ChronicleView/Chronic_vital_chart'
import { statecolor } from '../../Function/Wardstate'
import { formatxArrayhalf, formatendtime } from '../../Function/Timeformat'
import { simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'


class Flowsheetvitalsign extends Component {
    constructor(props) {
        super(props);
        this.state = {
            check_state_one: 0,
            datasource: null,
            statussource: 'CM',
            intervalselect: 0,
            displMode: 0
        };
      }
    
    componentDidMount() {
        this.setState({
            datasource: this.switchdata(this.state.intervalselect, this.state.statussource)
        })
    }
    switchdata(value_interval, sourcefrom) {
        function changevitaldays(vitaldays) {
            if (vitaldays === 3) {
                return 18
            }
            else {
                return 24
            }
        }

        function Interval_switch(interval) {
            if (interval === 0) {
                return 1
            }
            if (interval === 1) {
                return 3
            }
        }
        const { select_date } = this.props
        const interval = Interval_switch(value_interval)
        const starttime = formatendtime(select_date) - (interval * 24 * 60 * 60 * 1000)
        if (sourcefrom === 'NIS') {
            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationnisdata(datatime, changevitaldays(interval))
            return data
        }
        else {

            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationCentermonitordata(datatime, changevitaldays(interval))
            return data
        }
    }
    Interval_grid_box() {
        var gridarray = []
        for (let index = 0; index < 24; index++) {
            const number = index + 1
            if (number === this.state.check_state_one) {
                gridarray.push(<div style={{ width: "100%", height: 350, border: "rgba(245, 166, 35, 1) 1px solid" }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
            }
            else gridarray.push(<div style={{ width: "100%", height: 350 }} onClick={() => this.onClickCheckgrid_one(number)}></div>)
        }
        return gridarray
    }

    onClickCheckgrid_one(number) {
        // //1:7 2:8 3:9 ........
        // const { defaultdate } = this.props
        // const starttime = (defaultdate - (24 * 60 * 60 * 1000) + (7 * 60 * 60 * 1000)) + ((number - 1) * (60 * 60 * 1000))
        // const endtime = (defaultdate - (24 * 60 * 60 * 1000) + (7 * 60 * 60 * 1000)) + ((number) * (60 * 60 * 1000))
        // console.log(starttime, endtime)
        console.log(number)
        this.setState(
            {
                check_state_one: number

            }
        )
    }
    sourcehandleChange = (value) => {
        this.setState({
            statussource: value,
            datasource: this.switchdata(this.state.intervalselect, value)
        })

    }
    render() {
        const { Option } = Select;
        const { select_date } = this.props
        const viewwidth = window.screen.availWidth - 220
        const title_list = [{ item: "HR" }, { item: "ABP" }, { item: "NBP" }, { item: "SpO2" }, { item: "BT" }, { item: "RR" }]
        const vital_title = title_list.map((info, index) =>
            <div key={index} style={{ display: "flex", justifyContent: "center", alignItems: "center", height: "60px" }}>
                <div>
                    <t4 style={{ display: "flex", justifyContent: "center", color: statecolor(info.item) }}>{info.item}</t4>
                    <t1 style={{ display: "flex", justifyContent: "center", color: statecolor(info.item) }}>123</t1>
                </div>
            </div>
        )
        return (
            <div style={{ marginBottom: "20px" }}>
                <div style={{ display: 'flex', alignItems: 'center', height: '50px' }}>
                    <t1>生命徵象</t1>
                    <Select defaultValue={'CM'} style={{ width: 200, marginLeft: "20px" }} onChange={this.sourcehandleChange}>
                        <Option value={'CM'}>Center Monitor</Option>
                        <Option value={'NIS'}>NIS</Option>
                    </Select>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr" }}>
                    <div>
                        {vital_title}
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "repeat(6,60px)", position: "relative" }}>
                        <div style={{ position: "absolute", top: 5, left: 50, height: 350, width: viewwidth }}>
                            <div style={{ display: "grid", gridTemplateColumns: "repeat(24,1fr)" }}>
                                {this.Interval_grid_box()}
                            </div>
                        </div>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="hr_id_flootsheet" svg_id="hr_svg_flootsheet" circlrcolor="#779F50"></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="nbp_id_flootsheet" svg_id="nbp_svg_flootsheet" circlrcolor={"#C12528"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="abp_id_flootsheet" svg_id="abp_svg_flootsheet" circlrcolor={"#C12528"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="spo2_id_flootsheet" svg_id="spo2_svg_flootsheet" circlrcolor={"#4773B8"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="bt_id_flootsheet" svg_id="bt_svg_flootsheet" circlrcolor={"#7F3DB5"}></Chronicvitalchart>
                        <Chronicvitalchart statussource={this.state.statussource} datasource={this.state.datasource} max={200} min={0} select_interval={this.state.intervalselect} select_date={select_date} id_key="rr_id_flootsheet" svg_id="rr_svg_flootsheet" circlrcolor={"#DE7531"}></Chronicvitalchart>
                    </div>
                    {/* <div style={this.state.needFixed ? scrollstyle : stablestyle}><Chroniclescale patid='flootsheetscale'></Chroniclescale></div> */}
                </div>
            </div>
        );
    }
}
export default Flowsheetvitalsign