import React, { Component } from 'react';
import { Select } from 'antd';
import Examlabviewcell from './Examlabviewcell'
import { cbcdata, electrolysis } from '../../Data/Wardviewdata'

class Labview extends Component {
    state = {
        Datelist: [],
        selectstate: 0,
        data: null
    }
    componentDidMount() {
        function switchdata(key) {
            switch (key) {
                case 0:
                    return cbcdata
                case 1:
                    return electrolysis
                default:
                    return cbcdata
            }
        }
        const data = switchdata(this.state.selectstate)
        this.setState({
            data: data
        })
    }
    sendData = (value) => {
        this.props.parentDateCallback(value, this.state.selectstate);
    }

    callbackDateFunction = (value) => {
        this.sendData(value)
    }

    title_list(listarray) {
        const list = listarray.map((info, index) =>
            <centerstyle key={index}>
                <t2>{info}</t2>
            </centerstyle>
        )
        return list;
    }

    handleChange = (value) => {
        function switchdata(key) {
            switch (key) {
                case 0:
                    return cbcdata
                case 1:
                    return electrolysis
                default:
                    return cbcdata
            }
        }
        const data = switchdata(value)
        this.setState(
            {
                selectstate: value,
                data: data
            }
        )
    }
    render() {
        const { Option } = Select;
        const { lab_item_list, row_title } = this.props
        //0:CBC 1:電解質 2:肝功能 3:心臟 4:凝血 5:GAS 6:Glucose
        const select_list = lab_item_list.map((info, index) =>
            <Option key={index} value={index}>{info.item}</Option>
        )
        return (
            <div>
                <t1>LAB(常規檢驗)</t1>
                <div style={{ display: "grid", gridTemplateColumns: "auto 1fr", marginTop: "10px", gridColumnGap: "5px" }}>
                    <t2 style={{ display: "flex", alignItems: "center" }}>檢驗類別:</t2>
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <Select defaultValue={this.state.selectstate} style={{ width: "80%" }} onChange={this.handleChange}>
                            {select_list}
                        </Select>
                    </div>
                </div>
                <div style={{ marginTop: "10px", display: "grid", gridTemplateColumns: "110px 1fr", height: "50px", backgroundColor: "#E2EDF3" }}>
                    <centerstyle>
                        <t2>檢驗項目</t2>
                    </centerstyle>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(9,1fr)" }}>
                        {this.title_list(row_title[this.state.selectstate].title)}
                    </div>
                </div>
                <Examlabviewcell data={this.state.data} selectstate={this.state.selectstate} parentDateCallback={this.callbackDateFunction}></Examlabviewcell>
            </div>
        );
    }
}

export default Labview;