import React, { Component } from 'react';
import { MMDDhhmmfromat } from '../../Function/Timeformat'
import { Labwarningvalue } from '../../Function/Wardstate'

class Labviewitem extends Component {
    render() {
        function wbcdata(string) {
            if (string === 'WBC') {
                return true
            }
            return false
        }
        const { data } = this.props
        return (
            <div style={{ display: "grid", gridTemplateRows: "1fr 2fr 2fr", height: "120px" }}>
                <centerstyle>
                    <t3>{data.item}</t3>
                </centerstyle>
                <centerstyle>
                    <div style={{ fontSize: "24px", color: Labwarningvalue(data.value, data.item) ? "black" : "red" }}>{data.value}</div>
                </centerstyle>
                <div style={wbcdata(data.item) ? { display: "grid", gridTemplateRows: "1fr 1fr" } : null}>
                    <div style={wbcdata(data.item) ? { display: "block" } : { display: "none" }}>
                        <centerstyle>
                            <t3 style={{ color: "rgba(0,0,0,0.4)" }}>{data.value2}</t3>
                        </centerstyle>
                    </div>
                    <centerstyle>
                        <t3 style={{ color: "rgba(0,0,0,0.4)" }}>{MMDDhhmmfromat(data.time)}</t3>
                    </centerstyle>
                </div>
            </div>
        );
    }
}

export default Labviewitem