import React, { Component } from 'react'
import { DatePicker, Input } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import Nurtiontpncell from './Nurtion_TPN_cell'
import { tpn_row_data} from '../../Data/Wardviewdata-jack'

class Nurtiontpnorder extends Component {
    state = {
        onEdit: false,
        tpndata: []
    }
    componentDidMount(){
        this.setState({
            tpndata:tpn_row_data
        })
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    search_click() {
        const inputdata = document.getElementById("nurtion_feeding_input").value
        const datapicker = document.querySelectorAll("input.ant-calendar-picker-input")[1].value
        console.log(datapicker)
        if (inputdata == '' || inputdata == undefined || inputdata == null) {
            console.log("No input data")
        }
        else {
            console.log("Click search input = " + inputdata)

        }
        if (datapicker == '' || datapicker == undefined || datapicker == null) {
            console.log("No input data")
        }
        else {
            console.log("Click search input = " + datapicker)

        }

    }
    render() {
        function tpnrowdata(datalist) {
            const tpncell = datalist.map(function (info, index) {
                return (
                    <Nurtiontpncell key={index} data={info}></Nurtiontpncell>
                )
            })
            return tpncell;
        }

        const title = ["處置名稱", "狀態", "開始時間", "結束時間"]

        const titilecell = title.map(function (info, index) {
            return (
                <div key={index} style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                    <t2>{info}</t2>
                </div>
            );
        })
        return (
            <div>
                <t1>TPN order</t1>
                <div style={{ display: "grid", gridTemplateColumns: "220px 1fr 25px", gridColumnGap: "10px" }}>
                    <DatePicker id="nurtion_feeding_datepicker" placeholder={["請輸入日期"]} allowClear={false} />
                    <Input id="nurtion_feeding_input" placeholder="請輸入關鍵字" />
                    <div onClick={() => this.search_click()} style={{ display: "flex", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "370px 60px 1fr 1fr", background: "#cccccc", height: "50px", marginTop: "10px" }}>
                    {titilecell}
                </div>
                <div style={{ minHeight: "120px", maxHeight: "400px", overflowY: "auto" }}>
                    {tpnrowdata(this.state.tpndata)}
                </div>
            </div>
        );
    }
}

export default Nurtiontpnorder