import React, { Component } from 'react';
import { DatePicker } from 'antd';
import { Select } from 'antd';
import './Calender.scss'
import { mmddformat, compareday, yyyymmfirstweeksate } from '../../Function/Timeformat'
import Calendercell from './Calendercell'
import { Icon } from 'antd'
import Crossection_schedule from '../CrossSectionView/Crosssectionschedule'
class Calender extends Component {
    state = {
        calendermode: 'week',
        Clickdays: null,
        ClickDate: null,
        displayinfo: null
    }

    callbackFunction = (timestamp, clickindex) => {
        console.log(timestamp)
        this.setState({
            ClickDate: timestamp,
            displayinfo: clickindex
        })
    }
    onCancelClick() {
        this.setState({
            ClickDate: null,
            displayinfo: null
        })
    }
    handleChange = (value) => {
        console.log(value)
        this.setState({
            calendermode: value,
            displayinfo: null,
            Clickdays: null
        })
    }
    onChange = (timestamp) => {
        console.log('changestate')
    }
    onClickCheck(timestampdate) {
        this.setState({
            onClickdays: timestampdate
        })
    }
    weekmode() {
        var weekcontext = []
        for (let index = 0; index < 7; index++) {
            const timestampdate = new Date().getTime() - ((index - 3) * 24 * 60 * 60 * 1000)
            const date = mmddformat(new Date().getTime() - ((index - 3) * 24 * 60 * 60 * 1000))
            if (index - 3 === 0) {
                weekcontext.push(
                    <Calendercell clickindex={'week'} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={true} newDate={true} parentCallback={this.callbackFunction}></Calendercell>
                );
            }
            else {
                weekcontext.push(
                    <Calendercell clickindex={'week'} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={compareday(timestampdate)} newDate={false} parentCallback={this.callbackFunction}></Calendercell>
                );

            }
        }
        return weekcontext.reverse()
    }
    weekmonthmode(weeknumber, firstMonthWeekDay) {
        var weekcontext = []
        for (let index = 0; index < 7; index++) {
            const timestampdate = new Date(firstMonthWeekDay).getTime() + (index * 24 * 60 * 60 * 1000)
            const date = mmddformat(timestampdate)
            if (new Date(mmddformat(new Date().getTime())).getTime() <= new Date(mmddformat(new Date(timestampdate).getTime())).getTime()) {
                weekcontext.push(
                    <Calendercell clickindex={weeknumber} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={true} newDate={true} parentCallback={this.callbackFunction}></Calendercell>
                );
            }
            else {
                weekcontext.push(
                    <Calendercell clickindex={weeknumber} clickdate={this.state.ClickDate} timestamp={timestampdate} date={date} state={false} newDate={true} parentCallback={this.callbackFunction}></Calendercell>
                );
            }
        }
        return weekcontext
    }

    ShowModule(index) {
        function checkmode(state) {
            if (state === 'week') {
                return 'calenderdisplayinformation'
            }
            if (state === index) {
                return 'calenderdisplayinformation'
            }
            return 'calenderdisplayinformationnone'
        }
        return (
            <div className={checkmode(this.state.displayinfo)}>
                <div className='calenderModule'>
                    <t1>詳細資訊</t1>
                    <Icon type="close" onClick={() => this.onCancelClick()} />
                </div>
                <div className='Modulelayout'>
                    <Crossection_schedule></Crossection_schedule>
                </div>
            </div>
        );
    }


    setgroup() {
        var calendermonthgroup = []
        const monthfirstweekdate = new Date(yyyymmfirstweeksate(new Date())).getDay() - 1
        const firstMonthWeekDay = new Date(yyyymmfirstweeksate(new Date())).getTime() - (monthfirstweekdate * 24 * 60 * 60 * 1000)
        for (let index = 0; index < 5; index++) {
            calendermonthgroup.push(
                <div className='calendercontextMonthRows'>
                    {this.weekmonthmode(index, firstMonthWeekDay + (index * 7 * 24 * 60 * 60 * 1000))}
                    {this.ShowModule(index)}
                </div>
            );

        }
        return calendermonthgroup
    }
    modechange(state) {
        if (state === 'week') {
            return (
                <div className='calendercontext'>
                    {this.weekmode()}
                    {this.ShowModule()}
                </div>
            );
        }
        else {
            // return (
            //     <div>
            //         {this.setgroup()}
            //     </div>
            // );
            return this.setgroup()
        }
    }

    SelectDatepickertype() {
        const { MonthPicker } = DatePicker;
        if (this.state.calendermode === 'month') {
            return (
                <MonthPicker placeholder="請選擇月份" />
            );
        }
        else {
            return (
                <DatePicker placeholder={['請選擇時間']} />
            );
        }
    }
    render() {
        const { Option } = Select;
        // const localday = new Date().getTime()
        // function compareday(days) {
        //     if (new Date(days).getTime() > new Date().getTime()) {
        //         return true
        //     }
        //     return false
        // }
        return (
            <div>
                <t1 className='calendertitle'>Schedule</t1>
                <div className='calendercontrol'>
                    {this.SelectDatepickertype()}
                    <Select defaultValue={this.state.calendermode} onChange={this.handleChange}>
                        <Option value="week">週</Option>
                        <Option value="month">月</Option>
                    </Select>
                </div>
                {this.modechange(this.state.calendermode)}
            </div>
        );
    }
}

export default Calender