export function Select_Main_Doc(DOCLIST) {
    const MainDoc = DOCLIST.filter(function (value, index, array){
        return value.PDTYPE === "B"
    })
    if (MainDoc.length === 0) {
        return ""
    }
    else{
        const data = MainDoc[0]
        return data.PDDOCNMC
    }
}