import React, { Component } from 'react'
import { Input, Modal } from 'antd';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtionfeedingcell.scss'
import { yyyymmddformat } from '../../Function/Timeformat'

class Nurtionfeedingcell extends Component {
    state = {
        EditMode: false,
        onEdit: false
    }

    onMouseEnter() {
        this.setState(
            {
                EditMode: true
            }
        )
    }
    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    Moduledisplay() {
        const { TextArea } = Input;
        return (
            <Modal
                title="備註"
                visible={this.state.onEdit}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                <div style={{ display: "grid", gridTemplateRows: "30px 30px 30px 30px" }}>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>開立時間:</div>
                        <div>上次編輯時間:</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>處理名稱:</div>
                        <div>上次編輯人:</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                        <div>頻次:</div>
                        <div>開立醫師:</div>
                    </div>
                    <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>備註內容:</div>
                    <TextArea rows={5} />
                </div>
            </Modal>
        );
    }

    render() {
        const { data } = this.props
        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} style={{ position: "relative", height: "100px" }}>
                <div className='nurtionfeedingcell'>
                    <div className='cellforstate'>
                        <div className='celllayout'>
                            <t3>{data.FeedingOrderDispositionName}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{yyyymmddformat(data.FeedingOrderStartTime)}</t3>
                        </div>
                        <div className='celllayout'>
                            <t3>{yyyymmddformat(data.FeedingOrderEndTime)}</t3>
                        </div>
                    </div>
                    <div className='cellforremark'>
                        <t3>{data.FeedingOrderRemarks}</t3>
                    </div>
                </div>
                <div style={this.state.EditMode ? { display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", top: 0, right: 0, height: "100px", background: "white", width: "77px", border: "rgba(232,232,232,1) 1px solid" } : { display: "none" }}>
                    <div style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <img src={chooseedit} alt='chooseedit'></img>
                    </div>
                </div>
                {this.Moduledisplay()}
            </div>
        );
    }
}

export default Nurtionfeedingcell