import React, { Component } from 'react';
import Wardcarstatus from './Wardcardstatus'
import { statecolor } from '../Function/Wardstate'
import './Wardcardvalue.scss'

class Wardcardvalue extends Component {
    render() {
        function search_item(itemarray, string) {
            const filteritem = itemarray.filter(function (item, index, array) {
                return item.item === string;
            });
            return filteritem[0].data;
        }

        function filtervitalvaluehisid(HISID,itemarray){
            const filteritem = itemarray.filter(function (item, index, array) {
                return item.HISID === HISID;
            });
            if (filteritem.length === 0) {
                return []
            }
            return filteritem[0].vitalvalue;
        }
        const { wardvalue,flag ,HISID} = this.props
        //const vitalvalue = filtervitalvaluehisid(HISID,wardvalue)
        const vitalvalue = []
        if (vitalvalue.length === 0) {
            return null
        }
        return (
            <div className='Wardcardvaluelayout'>
                <div className='Wardcardvitalvalue'>
                    <div className='valueRows'>
                        <div>
                            <t4 style={{ display: "block", color: statecolor('HR') }}>HR</t4>
                            <t2 style={{ display: "block", color: statecolor('HR') }}>{search_item(vitalvalue, "HR")}</t2>
                        </div>
                        <div>
                            <t4 style={{ display: "block", color: statecolor('SpO2') }}>SpO<sub>2</sub></t4>
                            <t2 style={{ display: "block", color: statecolor('SpO2') }}>{search_item(vitalvalue, "SpO2")}</t2>
                        </div>
                    </div>
                    <div className='valueRows'>
                        <div>
                            <t4 style={{ display: "block", color: statecolor('BP') }}>ABP</t4>
                            <t2 style={{ display: "block", color: statecolor('BP') }}>{search_item(vitalvalue, "ABP_hight")}/{search_item(vitalvalue, "ABP_low")}({search_item(vitalvalue, "ABP_avg")})</t2>
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnsGap: "5px" }}>
                            <div>
                                <t4 style={{ display: "block", color: statecolor('RR') }}>RR</t4>
                                <t2 style={{ display: "block", color: statecolor('RR') }}>{search_item(vitalvalue, "RR")}%</t2>
                            </div>
                            <div>
                                <t4 style={{ display: "block", color: statecolor('BT') }}>BT</t4>
                                <t2 style={{ display: "block", color: statecolor('BT') }}>{search_item(vitalvalue, "BT")}</t2>
                            </div>
                        </div>
                    </div>
                </div>
                <Wardcarstatus flag={flag}></Wardcarstatus>
            </div>
        );
    }
}

export default Wardcardvalue