import React, { Component } from 'react';
import WardChart from './Wardchart'
import Wardinfousercheckbox from './Wardunfousercheckbox'
import Wardlabview from './Wardlab'
import { Modal, Select } from 'antd';
import Wardgrewchart from './Wardgrewchart'
import { gender } from '../Function/Wardbedfunction'
import { yyyymmddformat } from '../Function/Timeformat'
import { pregancybirthdayweekday, pregancynowweek } from '../Function/Wardbedfunction'
import fileLogo from '../Image/svg/file1.svg'
import file2Logo from '../Image/svg/file2.svg'
import './Wardinfouser.scss'
import { wardstatestringformat } from '../Function/Wardstate'

class Wardinfouser extends Component {
    state = {
        alertstate: "none",
        Data_change: true,
        visible: false,
        source: false
    }

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };


    Medgroupclick() {
        this.setState(
            {
                alertstate: "block"
            }
        )
    }


    CancelMedgroupclick() {
        this.setState(
            {
                alertstate: "none"
            }
        )
    }

    Datachange() {
        this.setState(
            {
                Data_change: !this.state.Data_change
            }
        )
    }

    Switchitem(item) {
        switch (item.item) {
            case "HR":
                return item.data
            case "BP":
                return item.data
            case "SpO2":
                return item.data
            case "BT":
                return item.data
            case "RR":
                return item.data
            default:
                return null
        }
    }

    sendData = (idnumber) => {
        this.props.parentCallback(idnumber);
    }



    Birthday_format(bir) {
        function Monthformat(month) {
            return month + 1
        }
        const bir_time = new Date(bir).getFullYear() + "-" + Monthformat(new Date(bir).getMonth()) + '-' + new Date(bir).getDate()
        return bir_time
    }
    sourcehandleChange = (value) => {
        // console.log(`selected ${value}`);
        switch (value) {
            case 0:
                this.switch_source(false)
                break;
            case 1:
                this.switch_source(true)
                break;
            default:
                break;
        }

    }
    switch_source(bool) {
        this.setState(
            {
                source: bool
            }
        )
    }
    render() {
        //search CASEID
        const userinfo = this.props.data
        const { wardvalue } = this.props
        const flag = userinfo.flag
        function statusspan(source) {
            if (source.item === 'Respirator') {
                return 'gridtwocoltwo'
            }
            else return 'gridtwocolone'
        }
        function style_change(source) {
            if (source.item === 'Respirator') {
                if (source.data === 'false') {
                    return 'flagfalse'
                }
                if (source.data === 'non-invasive') {
                    return 'flagtrue'
                }
                return 'flagtrue'
            }
            else {
                if (source.data) {
                    return 'flagtrue'
                }
                else {
                    return 'flagfalse'
                }
            }
        }
        function flaglist(flag) {
            var flaglist = []
            for (let index = 0; index < flag.length; index++) {
                const element = flag[index];
                flaglist.push(
                    <t4 className={style_change(element)}>{wardstatestringformat(element)}</t4>
                )

            }
            return flaglist
        }
        return (
            <div className='wardinfouserlayout'>
                <Modal
                    width={480}
                    title="醫療資訊團隊"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    footer={null}>
                    <div>
                        <div className='wardinfousermodelinfo'><t2>團隊人員</t2></div>
                        <div className='wardinfousermodelinfocontext'>
                            <div className='wardinfousercontextinfo'><t2>主治醫師 陳ＯＯ</t2></div>
                            <div className='wardinfousercontextinfo'><t2>住院醫師 劉ＯＯ</t2></div>
                            <div className='wardinfousercontextinfo'><t2>專科護理師 林ＯＯ</t2></div>
                            <div className='wardinfousercontextinfo'><t2>護理師 遊ＯＯ</t2></div>
                        </div>
                    </div>
                </Modal>
                <div className='wardinfopatient'>
                    <div style={{ height: '50px', display: 'grid', gridTemplateColumns: "1fr 1fr 1fr" }}>
                        <div style={{ alignItems: "center", display: "flex" }}>
                            <t2 className='wardblacktoindex' onClick={() => this.sendData(null)}>&larr;返回總覽</t2>
                        </div>
                        <centerstyle>
                            <t1>病人資訊摘要</t1>
                        </centerstyle>
                    </div>
                    <div style={{ height: "50px" }}>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <t2>床號:{userinfo.bednumber},{userinfo.name},{yyyymmddformat(userinfo.birthday)},{userinfo.HISID}</t2>
                            <div className='wardinfouserflagboard'>
                                {flaglist(flag)}
                            </div>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <div style={{ display: "flex", alignItems: "center" }}>
                                <t2 style={{ marginRight: "16px" }}>{pregancybirthdayweekday(userinfo.pregnancy, userinfo.birthday)}&rarr;{pregancynowweek(userinfo.pregnancy)}</t2>
                                <t4 style={{ marginRight: "8px" }}>{userinfo.weightbirth}g&rarr;{userinfo.weight}g</t4>
                                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", width: "70px" }}>
                                    <img src={fileLogo} alt='fileLogo'></img>
                                    <t4>{userinfo.report}</t4>
                                    <img src={file2Logo} alt='file2Logo'></img>
                                    <t4>{userinfo.nonreport}</t4>
                                </div>
                            </div>
                            <div style={{display:"grid",gridTemplateColumns:"auto auto",gridColumnGap:"10px"}}>
                                <t3>主治醫師 {userinfo.maindoctor}</t3>
                                <t3 className='wardblacktoindex' onClick={this.showModal}>查看醫療團隊資訊</t3>
                            </div>
                        </div>
                    </div>
                    <div className='wardinfousercontent'>
                        <div style={{ padding: "10px 15px" }}>
                            <WardChart wardvalue={wardvalue} HISID={userinfo.HISID}></WardChart>
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px" }}>
                            <div style={{ borderRadius: "4px" }}>
                                <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                    <t2>檢驗項目</t2>
                                </div>
                                <Wardlabview></Wardlabview>
                            </div>
                            <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                                <Wardgrewchart userinfo={userinfo}></Wardgrewchart>
                            </div>
                        </div>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px", marginTop: "10px" }}>
                            <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2>病人重要注意事項</t2>
                            </div>
                            <div style={{ padding: "0px 15px", maxHeight: "200px", minHeight: "50px" }}>
                                <div style={{ padding: "5px 0px" }}>
                                    <t3>1.對甲殼類食物過敏</t3>
                                </div>
                            </div>
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px", marginTop: "10px" }}>
                            <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                                <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                    <t2>排程</t2>
                                </div>
                                <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                    <div style={{ height: "50px", display: 'grid', gridTemplateColumns: "25% 75%", borderBottomWidth: "1px", borderBottomColor: "rgba(232, 232, 232, 1)", borderBottomStyle: "solid" }}>
                                        <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>6/16 16:00</div>
                                        <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>個案家屬來訪</div>
                                    </div>
                                </div>
                            </div>
                            <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                                <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                    <t2>待辦事項</t2>
                                </div>
                                <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                    <Wardinfousercheckbox selectstate={true}></Wardinfousercheckbox>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Wardinfouser;