import React, { Component } from 'react';
import * as d3 from "d3";
import './Crosssectionvitalsignchart.scss'
import { formatendtime, formatintervaldays } from '../../Function/Timeformat'
import { simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'

class Crosssectionvitalsignchart extends Component {
    state = {
        change: false
    }
    componentDidUpdate() {

        const { svg_id } = this.props
        d3.select("#" + svg_id).remove()
        this.drawChart()
    }

    componentDidMount() {
        this.drawChart();
    }
    drawChart() {
        function formatxArray(time, interval) {
            var xAxisarray = []
            if (interval) {
                for (let index = 0; index < 7; index++) {
                    xAxisarray.push(time + (index * 60 * 60 * 1000 * 4))
                }
            }
            else {
                for (let index = 0; index < 10; index++) {
                    xAxisarray.push(time + (index * 60 * 60 * 1000 * 8))
                }

            }
            return xAxisarray
        }

        function formatbotaxis(time, interval) {
            var xAxisarray = []
            if (interval) {
                xAxisarray.push(time + (17 * 60 * 60 * 1000))
            }
            else {
                for (let index = 0; index < 3; index++) {
                    xAxisarray.push(time + (((index * 24) + 17) * 60 * 60 * 1000))
                }

            }
            return xAxisarray
        }

        var formatHour = d3.timeFormat("%H")

        var formatDay = d3.timeFormat("%b %d")
        function multiFormat(date) {
            return formatHour(date)
        }

        function timeBotmultiFormat(date) {
            return formatDay(date)
        }
        //vitaldays true:threeday
        const { id_key, svg_id, datasource, axisBot, axisTop, circlrcolor, colorshadow, max, min, defaultdate, source, vitaldays } = this.props
        const width = window.screen.availWidth * 0.25, height = 100, viewwidth = width - 50, rect_width = width / 46
        const EndTime = formatendtime(defaultdate)
        var vital_interval = formatintervaldays(EndTime, vitaldays)

        var y = d3.scaleLinear()
            .domain([min, max])
            .range([height, 0])

        var x = d3.scaleLinear()
            .domain([vital_interval, EndTime])
            .range([0, viewwidth])


        var yAxis = d3.axisLeft(y)
            .tickValues([0, 200])
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(15);


        const xAxisvalue = formatxArray(vital_interval)
        const xAxisbotvalue = formatbotaxis(vital_interval)
        var xAxis = d3.axisTop(x)
            .tickValues(xAxisvalue)
            .tickSize(0, 0)
            .tickPadding(5)
            .tickFormat(function (d) { return multiFormat(d); });

        var xAxisbot = d3.axisBottom(x)
            .tickValues(xAxisbotvalue)
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) { return timeBotmultiFormat(d) });



        var svg
        if (axisTop) {
            svg = d3.select("#" + id_key)
                .append("svg")
                .attr("id", svg_id)
                // .attr("viewBox", [0, 0, width, height + 25])
                .style("width", width)
                .style("height", height + 25)
                .append("g")
                .attr("transform", "translate(" + 40 + "," + 20 + ")");
        }
        else {
            if (axisBot) {
                svg = d3.select("#" + id_key)
                    .append("svg")
                    .attr("id", svg_id)
                    // .attr("viewBox", [0, 0, width, height + 25])
                    .style("width", width)
                    .style("height", height + 25)
                    .append("g")
                    .attr("transform", "translate(" + 40 + "," + 5 + ")");
            }
            else {

                svg = d3.select("#" + id_key)
                    .append("svg")
                    .attr("id", svg_id)
                    // .attr("viewBox", [0, 0, width, height + 10])
                    .style("width", width)
                    .style("height", height + 10)
                    .append("g")
                    .attr("transform", "translate(" + 40 + "," + 5 + ")");
            }
        }

        if (axisTop) {
            //console.log(axisTop)
            svg.append("g")
                .call(xAxis)
                .attr("class", "axis");
        }

        if (axisBot) {
            svg.append("g")
                .call(xAxisbot)
                .attr("class", "axis");

        }
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");


        svg.append('line')
            .attr('x1', x(vital_interval))
            .attr('y1', y(max))
            .attr('x2', x(EndTime))
            .attr('y2', y(max))
            .style('stroke', 'rgba(220,220,220,1)')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', x(vital_interval))
            .attr('y1', y(0))
            .attr('x2', x(EndTime))
            .attr('y2', y(0))
            .style('stroke', 'rgba(220,220,220,1)')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        if (vitaldays === 3) {
            for (let index = 0; index < 19; index++) {
                if (index % 2 === 0) {
                    svg.append('line')
                        .attr('x1', x(vital_interval + (index * 60 * 60 * 1000 * 4)))
                        .attr('y1', 0)
                        .attr('x2', x(vital_interval + (index * 60 * 60 * 1000 * 4)))
                        .attr('y2', height)
                        .style('stroke', 'black')
                        .style('stroke-width', 2);
                }
                else {
                    svg.append('line')
                        .attr('x1', x(vital_interval + (index * 60 * 60 * 1000 * 4)))
                        .attr('y1', 0)
                        .attr('x2', x(vital_interval + (index * 60 * 60 * 1000 * 4)))
                        .attr('y2', height)
                        .style('stroke', 'rgba(220,220,220,1)')
                        .style('stroke-width', 1)
                        .style('stroke-dasharray', 5.5);
                }
            }
        }
        else {
            for (let index = 0; index < 25; index++) {
                if (index % 8 === 0) {
                    svg.append('line')
                        .attr('x1', x(vital_interval + (index * 60 * 60 * 1000)))
                        .attr('y1', 0)
                        .attr('x2', x(vital_interval + (index * 60 * 60 * 1000)))
                        .attr('y2', height)
                        .style('stroke', 'black')
                        .style('stroke-width', 2);
                }
                else {
                    svg.append('line')
                        .attr('x1', x(vital_interval + (index * 60 * 60 * 1000)))
                        .attr('y1', 0)
                        .attr('x2', x(vital_interval + (index * 60 * 60 * 1000)))
                        .attr('y2', height)
                        .style('stroke', 'rgba(220,220,220,1)')
                        .style('stroke-width', 1)
                        .style('stroke-dasharray', 5.5);
                }
            }
        }


        function drawshadow(data,number) {
            console.log(data)
            for (let index = 0; index < data.length; index++) {
                const borderwidth = viewwidth/number
                const element = data[index];
                svg.append('line')
                    .attr('x1', x(element.time))
                    .attr('y1', y(20))
                    .attr('x2', x(element.time))
                    .attr('y2', y(120))
                    .style('stroke', circlrcolor)
                    .style('opacity', 0.5)
                    // .style('stroke', 'red')
                    .style('stroke-width', borderwidth)


            }
        }
        if (datasource === null) {

        }
        else {
            if (source) {
                // const datatime = formatxArrayhalf(vital_interval, changevitaldays(vitaldays))
                // const data = simulationnisdata(datatime, changevitaldays(vitaldays))
                drawshadow(datasource,datasource.length)
                for (let index = 0; index < datasource.length; index++) {
                    const element = datasource[index];
                    svg.append('circle')
                        .attr('cx', x(element.time))
                        .attr('cy', y(element.data))
                        .attr('r', 3)
                        .style('fill', circlrcolor)

                }
            }
            else {
                drawshadow(datasource,datasource.length)
                // const datatime = formatxArrayhalf(vital_interval, changevitaldays(vitaldays))
                // const sim_data = simulationCentermonitordata(datatime, changevitaldays(vitaldays))
                for (let index = 0; index < datasource.length; index++) {
                    const element = datasource[index];
                    const eledata = element.data
                    console.log(element.time)
                    const boxwidth = 10
                    const data_sorted = eledata.sort(d3.ascending)
                    const q1 = d3.quantile(data_sorted, .25)
                    // const median = d3.quantile(data_sorted, .5)
                    const q3 = d3.quantile(data_sorted, .75)
                    const interQuantileRange = q3 - q1
                    const minbox = q1 - 1.5 * interQuantileRange
                    const maxbox = q1 + 1.5 * interQuantileRange
                    svg
                        .append("line")
                        .attr("x1", x(element.time))
                        .attr("x2", x(element.time))
                        .attr("y1", y(minbox))
                        .attr("y2", y(maxbox))
                        .attr("stroke", circlrcolor)
                        .style('stroke-width', 1)

                    svg.append("rect")
                        .attr("x", x(element.time) - boxwidth / 2)
                        .attr("y", y(q3))
                        .attr("height", (y(q1) - y(q3)))
                        .attr("width", boxwidth)
                        .attr("stroke", circlrcolor)
                        .style("fill", "rgba(255,255,255,1)")
                        .style('stroke-width', 1);
                    svg
                        .selectAll("toto")
                        .data([minbox, maxbox])
                        .enter()
                        .append("line")
                        .attr("x1", x(element.time) - boxwidth / 2)
                        .attr("x2", x(element.time) + boxwidth / 2)
                        .attr("y1", function (d) { return (y(d)) })
                        .attr("y2", function (d) { return (y(d)) })
                        .attr("stroke", circlrcolor)
                        .style('stroke-width', 1);

                }
            }
        }
    }

    changestate() {
        this.setState({
            change: !this.state.change
        })
    }
    render() {
        const { svg_id, source, id_key } = this.props
        return (
            <div id={id_key}>
            </div>
        );
    }
}

export default Crosssectionvitalsignchart;