import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import { Modal, Input } from 'antd'
import './Prescriptionordertablecell.scss'

class Prescriptionordertablecell extends Component {
    state = {
        onEdit: false,
        onMove: false
    }

    on_Mouse() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: true
                }
            )
        }
    }
    on_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: false
                }
            )
        }
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    render() {
        const { info } = this.props
        const { TextArea } = Input;
        return (
            <div onMouseMove={() => this.on_Mouse()} onMouseLeave={() => this.on_leave()} className='prescriptiontablecell'>
                <div style={{ display: "grid", gridTemplateRows: "80px 50px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1.5fr 0.5fr 1fr 1fr" }}>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{info.Name}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{info.ProductName}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{info.Unit}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{info.State}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{info.StartTime}</t3></div>
                        <div className='prescriptiontablecellcontext'><t3 className='prescriptiontablecellcontexttext'>{info.EndTime}</t3></div>
                    </div>
                    <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{info.Note}</t3>
                    <div style={this.state.onMove ? { display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: "130px" ,background:"white",width:"77px",border:"rgba(238,238,238,1) 1px solid"} : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <Modal
                    title="備註"
                    visible={this.state.onEdit}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                >
                    <div style={{ display: "grid", gridTemplateRows: "30px 30px 30px 30px" }}>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <div>開立時間:</div>
                            <div>上次編輯時間:</div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <div>處理名稱:</div>
                            <div>上次編輯人:</div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                            <div>頻次:</div>
                            <div>開立醫師:</div>
                        </div>
                        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>備註內容:</div>
                        <TextArea rows={5} />
                    </div>
                </Modal>
            </div>
        );
    }
}

export default Prescriptionordertablecell