import React, { Component } from 'react'
import { DatePicker, Input, Button } from 'antd'
import Rttable from './RT_table'
import searchlogo from '../../Image/svg/Search.svg'

class RTsubview extends Component {
    TableSelect(item) {
        switch (item) {
            case 0:
                return (<Rttable gridcount={"Gas"} />);
            case 1:
                return (<Rttable gridcount={"Breath"} />);
            case 2:
                return (<Rttable gridcount={"xRay"} />);
            default:
                break;
        }
    }

    title(item) {
        switch (item) {
            case 0:
                return "Gas 血氧分析";
            case 1:
                return "呼吸器使用紀錄";
            case 2:
                return "X-Ray";
            default:
                return null
        }

    }

    onClickSearch(){
        console.log('click search')
    }

    render() {
        const { RangePicker } = DatePicker;
        function onChange(date, dateString) {
            console.log(date, dateString);
        }
        const { tableState } = this.props
        return (
            <div>
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', height: '40px' }}>
                    <t1>{this.title(tableState)}</t1>
                </div>
                <div style={{ display: 'grid', gridTemplateColumns: "220px 25px",gridColumnGap:"10px" }}>
                    <RangePicker onChange={onChange} placeholder={["請輸入日期", "請輸入日期"]} allowClear={false} />
                    <div onClick={() => this.onClickSearch()} style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div>
                </div>
                {this.TableSelect(tableState)}
            </div>
        );
    }
}

export default RTsubview