import React, { Component } from 'react';
class Examlabviewcell extends Component {
    state = {
        select: false
    }
    sendData = (value) => {
        this.props.parentDateCallback(value);
    }
    onChange(value) {
        this.sendData(value)
        this.setState({
            select: !this.state.select
        })
    }
    render() {
        function datalist(listarray) {
            var element = []
            for (let index = 0; index < 9; index++) {
                if (index < listarray.length) {
                    element.push(
                        <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                            <t2>{listarray[index]}</t2>
                        </centerstyle>
                    )
                }
                else {
                    element.push(
                        <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                            <t2></t2>
                        </centerstyle>
                    )
                }
            }
            return element;
        }
        function arraylist(data) {
            console.log(data)
            const datalistarray = data.map(function (info, index) {
                return (
                    <div key={index} style={{ display: "grid", gridTemplateColumns: "110px 1fr", height: "50px" }}>
                        <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                            <t2 style={{ textAlign: "center" }}>{info.time}</t2>
                        </centerstyle>
                        <div style={{ display: "grid", gridTemplateColumns: "repeat(9,1fr)" }}>
                            {datalist(info.data)}
                        </div>
                    </div>
                )
            })
            return datalistarray
        }
        const { selectstate, data } = this.props
        const row_title = [
            { item: 0, title: ["WBC", "Hb", "PLT"] },
            { item: 1, title: ["Na", "K", "Ca(Free,Total)", "Mg", "P", "BUN", "Cr"] },
            { item: 2, title: ["AST", "ALT", "rGGT", "Alkp", "Alb"] },
            { item: 3, title: ["ck", "ck-MB", "BNP", "Troponin I"] },
            { item: 4, title: ["PT", "APiT", "INR"], string: ["PT", "APiT", "INR"] },
            { item: 5, title: ["PH", "pCO2", "pO2", "HCO3", "BE", "O2", "TCO2", "SaCO2"] },
            { item: 6, title: ["糖化血色素", "空腹血糖"] }
        ]
        const itemtime = '2019-01-01 15:00'
        if (data === null || data === undefined) {
            return null
        }
        else {
            return arraylist(data)
        }
    }
}

export default Examlabviewcell