import React, { Component } from 'react';
import { DatePicker, Select, Button } from 'antd';
import Notelist from './Notelist'
import searchlogo from '../../Image/svg/Search.svg'
import './Note.scss'
import demonote from '../../Image/demonote.png'


var dataRange = [];
class Noteview extends Component {
    state = {
        StartTime: null,
        EndTime: null,
        display_data: null,
        context: null,
        contentdisplay: false

    }
    callbackFunction = (item_array) => {
        console.log(item_array)
        this.setState(
            {
                display_data: item_array.NoteTime,
                context: item_array.Content,
                contentdisplay: true
            }
        )

    }
    handleChange = (value) => {

        console.log(`selected ${value}`);
        // this.setState({
        //     selectRangeDate: !this.state.previewmode,
        // });
    }
    onClickSearch() {
        if (dataRange.length !== 0) {
            this.setState(
                {
                    StartTime: dataRange[0],
                    EndTime: dataRange[1]

                }
            )
        }

    }
    onChange = (date, dateString) => {
        console.log(date, dateString);
        dataRange = []
        dataRange = dateString
    }
    render() {
        const note_data = [{
            "NoteTime": new Date('2019/12/5 7:00:00'),
            "Type": "Progress Note",
            "Content": "notenotenote",
        }]
        const { RangePicker } = DatePicker;
        const { Option } = Select;

        function handleChange(value) {
            console.log(`selected ${value}`);
        }
        const select_data = [
            { item: "Progress Note" },
            { item: "Nursing Note" },
            { item: "急診紀錄" },
            { item: "會診紀錄" },
            { item: "手術記錄" },
            { item: "社工紀錄" },
            { item: "藥師紀錄" },
            { item: "物理治療師紀錄" },
            { item: "呼吸治療師紀錄" }
        ]
        const select_option = select_data.map((info, index) =>
            <Option key={index} value={index}>{info.item}</Option>
        )
        return (
            <div className='Notelayout'>
                <div style={{ display: "grid", gridTemplateColumns: "200px 220px 25px", marginBottom: "10px" }}>
                    <Select defaultValue={0} style={{ width: 190 }} onChange={handleChange}>
                        {select_option}
                    </Select>
                    <RangePicker id="note_Range" placeholder={["請輸入日期", "請輸入日期"]} onChange={this.onChange} allowClear={false} />
                    <div onClick={() => this.onClickSearch()} style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div>
                </div>
                <div>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 7fr", gridGap: "10px" }}>
                        <div>
                            <div style={{ backgroundColor: "#e2edf3", height: "50px", padding: '15px' }}>開立日期</div>
                            <div style={{ padding: "10px" }}>
                                <Notelist display_data={this.state.display_data} parentCallback={this.callbackFunction} note_data={note_data} start={this.state.StartTime} End={this.state.EndTime}></Notelist>
                            </div>
                        </div>
                        <div>
                            <div style={{ backgroundColor: "#e2edf3", height: "50px", padding: '15px' }}>內容</div>
                            <div style={{ fontSize: "1.3rem" }} style={{ display: this.state.contentdisplay ? 'block' : 'none' }}>
                                <img src={demonote} alt='demonote' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Noteview;