import React, { Component } from 'react';
import { Modal } from 'antd'
import { bednumberformat, gender, birthdayweeks } from '../Function/Wardbedfunction'
import './Patientviewinfo.scss'
import { yyyymmddformat } from '../Function/Timeformat';
class Patientinfo extends Component {

    state = { visible: false };
    render() {
        const { patientdata } = this.props
        return (
            <div className='patientinfolayout'> 
                <div className='patientintitle'>病床:{bednumberformat(patientdata.bednumber)}/{patientdata.name}/{gender(patientdata.gender)}/{birthdayweeks(patientdata.birthday)}週歲</div>
                <div className='context'>
                    <div className='title'>病歷號碼:</div>
                    <div className='item'>{patientdata.PHISTNUM}</div>
                </div>
                <div className='context'>
                    <div className='title'>住院編號:</div>
                    <div className='item'>{patientdata.PCASENO}</div>
                </div>
                <div className='context'>
                    <div className='title'>主治醫師:</div>
                    <div className='item'>{patientdata.MainDoctor}醫師</div>
                </div>
                <div className='context'>
                    <div className='title'>轉入日期:</div>
                    <div className='item'>{yyyymmddformat(patientdata.IcuInTime)}</div>
                </div>
                <div className='context'>
                    <div className='titlenote'>備註:</div>
                    <div className='item'>{patientdata.note}</div>
                </div>
            </div>
        );
    }
}

export default Patientinfo