import React, { Component } from 'react';
import {
    iodatain,
    iodataout,
    iodatainthirdday,
    iodataoutthirdday,
    iodatain_crossection,
    iodataout_crossection,
    iodatasum_crossection,
    iodatasum_chronic_oneday,
    iodatasum_chronic_thirdday
} from '../Data/Wardviewdata'
import './iopage.scss'
import * as ApiProvider from '../API/ApiProvider'
import { nisapiyyyymmddformat } from '../Function/Timeformat'
import * as FiliterReg from '../Function/FiliterReg'

class IOpage extends Component {
    state = {

    }

    componentDidMount() {
        
        function QDC_TimeInterval(iostate, PickerDate, userdata) {
            const End_date = nisapiyyyymmddformat(new Date(PickerDate).getTime() + (24 * 60 * 60 * 1000))
            if (iostate === 0) {
                const Start_date = nisapiyyyymmddformat(new Date(PickerDate).getTime())
                return {
                    "st": Start_date,
                    "et": End_date,
                    "caseno": "25430982"
                }
            }
        }
        const { iostate, PickerDate, userdata } = this.props
        const apidata = QDC_TimeInterval(iostate, PickerDate, userdata)
        if (apidata === undefined) return null

    }
    render() {
        function titlelab(array) {
            const result = array.map(function (info, index) {
                return (
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                        <t2>{info}</t2>
                    </centerstyle>
                );
            })
            return result
        }
        function changetext(text) {
            if (text === 0) {
                return ''
            }
            return text
        }
        function displayiodata(list) {
            var arraylistdisplay = []
            var myNewArray = [];
            for (var i = 0; i < list.length; ++i) {
                for (var j = 0; j < list[i].length; ++j) {
                    myNewArray.push(list[i][j]);
                }
            }
            var arraylistdisplay = myNewArray.map(function (info, index) {
                return (
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                        <t2>{changetext(info)}</t2>
                    </centerstyle>
                )
            })
            return arraylistdisplay
        }
        function datalist(DataObj) {
            const iodata = DataObj.data
            return iodata
        }

        function arraysum(data) {
            const totalarray = data.data
            var arr = [];
            for (var i = 0; i < totalarray[0].length; i++) {
                arr[i] = []
                for (var j = 0; j < totalarray.length; j++) {
                    arr[i][j] = totalarray[j][i]
                }
            }
            var arr2 = [];
            for (let index = 0; index < arr.length; index++) {
                const element = arr[index];
                var total = 0
                for (let index = 0; index < element.length; index++) {
                    const itemsum = element[index];
                    total = total + parseFloat(itemsum)
                }
                arr2.push(total)
            }
            return arr2
        }
        const titleInarray = ['Feed', 'IV', 'Other', 'In:Sum']
        const titleOutarray = ['U/O', 'ml/kg/hr', 'Other', 'Out:Sum']

        const { iostate, PickerDate, userdata } = this.props

        const data_array_in = datalist(datachange(iostate, 'in'))
        const data_array_sum = datalist(datachange(iostate, 'sum'))
        const data_array_out = datalist(datachange(iostate, 'out'))


        function datachange(key, type) {
            if (type === 'in') {
                switch (key) {
                    case 0:
                        return { 'data': iodatain_crossection, 'key': key }
                    case 1:
                        return { 'data': iodataout, 'key': key }
                    case 2:
                        return { 'data': iodatainthirdday, 'key': key }
                    default:
                        break;
                }
            }
            else if (type === 'out') {
                switch (key) {
                    case 0:
                        return { 'data': iodataout_crossection, 'key': key }
                    case 1:
                        return { 'data': iodatain, 'key': key }
                    case 2:
                        return { 'data': iodataoutthirdday, 'key': key }
                    default:
                        break;
                }
            }
            else {
                switch (key) {
                    case 0:
                        return { 'data': iodatasum_crossection, 'key': key }
                    case 1:
                        return { 'data': iodatasum_chronic_oneday, 'key': key }
                    case 2:
                        return { 'data': iodatasum_chronic_thirdday, 'key': key }
                    default:
                        break;
                }
            }

        }
        function changetablestyle(key) {
            switch (key) {
                case 0:
                    return 'pagetableoncrossection'
                case 1:
                    return 'pagetableonchroniconday'
                case 2:
                    return 'pagetableonchronicontwodays'
                case 3:
                    return 'pagetableonchroniconweek'
                case 4:
                    return 'pagetableonchronicontwoweeks'
                default:
                    return null;
            }
        }
        return (
            <div style={{ display: "grid", gridTemplateRows: "4fr 4fr 1fr", height: "360px" }}>
                <div className={changetablestyle(iostate)}>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr 1fr" }}>
                        {titlelab(titleInarray)}
                    </div>
                    <div className='cell'>
                        {displayiodata(data_array_in)}
                    </div>
                </div>
                <div className={changetablestyle(iostate)}>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr 1fr" }}>
                        {titlelab(titleOutarray)}
                    </div>
                    <div className='cell'>
                        {displayiodata(data_array_out)}
                    </div>
                </div>
                <div className={changetablestyle(iostate)}>
                    <centerstyle><t2>I/O</t2></centerstyle>
                    <div className='cellsum'>
                        {displayiodata(data_array_sum)}
                    </div>
                </div>
            </div>
        );
    }

}

export default IOpage;