import React, { Component } from 'react';
import { io_source_1, io_source_2, io_source_3, io_source_4 } from '../../Data/Wardviewdata-jack'
import './Flootsheetio.scss'

class Flootsheetio extends Component {
    state = {
        itemNumber: "day"
    }


    IO_table(item) {
        function changetext(text) {
            if (text === 0) {
                return ''
            }
            return text
        }
        const data = item.data.map((info, index) =>
            <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }}>
                <t2>{changetext(info)}</t2>
            </centerstyle>
        )
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px repeat(24,1fr)", height: "50px" }}>
                <centerstyle style={{ background: "#cccccc" }}>
                    <div style={{ textAlign: "center" }}>
                        <t4>{item.item}</t4>
                    </div>
                </centerstyle>
                {data}
            </div>
        );
    }


    render() {
        const flootsheetio_table_1 = io_source_1.map((info, index) => <div key={index}>{this.IO_table(info)}</div>)
        const flootsheetio_table_2 = io_source_2.map((info, index) => <div key={index}>{this.IO_table(info)}</div>)
        const flootsheetio_table_3 = io_source_3.map((info, index) => <div key={index}>{this.IO_table(info)}</div>)
        const flootsheetio_table_4 = io_source_4.map((info, index) => <div key={index}>{this.IO_table(info)}</div>)
        return (
            <div style={{ marginTop: "20px" }}>
                <div style={{ display: 'flex', alignItems: 'center', height: '50px' }}>
                    <t1>I/O</t1>
                </div>
                <div className='flootiotable'>
                    {flootsheetio_table_1}
                </div>
                <div className='flootiotable'>
                    {flootsheetio_table_2}
                </div>
                <div className='flootiotable'>
                    {flootsheetio_table_3}
                </div>
                <div className='flootiotable'>
                    {flootsheetio_table_4}
                </div>
            </div>
        );
    }
}

export default Flootsheetio;