import React, { Component } from 'react';
import editlogo from '../../Image/svg/Edit.svg'
import detaillogo from '../../Image/svg/details.svg'
import { Input, Button } from 'antd';
import './Bedsidecard.scss'
import * as ApiProvider from '../../API/ApiProvider';

class Bedsidecard extends Component {
    state = {
        userData: this.props.userdata,
        EditMode: false,
        remarkContent: "",
        transinno: "4"
    }
    componentDidMount() {
        //bind function
        this.onInputChange = this.onInputChange.bind(this);

        //query patient remark
        this.queryPatientRemark();
    }
    queryPatientRemark() {
        ApiProvider.getOnePatientRemark(this.state.transinno)
        .then(data => {
            console.log("queryPatientRemark OK " + JSON.stringify(data));
            this.setState({
                remarkContent: data.data[0].context
            });
        })
        .catch(err => {
            console.log("queryPatientRemark NG " + JSON.stringify(err));
        });
    }
    onInputChange(event) {
        this.setState({inputContent: event.target.value})
    }
    onChangeEditMode() {
        this.setState(
            {
                EditMode: !this.state.EditMode
            }
        )
    }

    onClickCancel() {
        this.setState(
            {
                EditMode: false
            }
        )
    }

    onClickEnter() {
        ApiProvider.createPatientRemark(this.state.inputContent, this.state.userData.HISID, this.state.transinno)
        .then(data => {
            console.log("createPatientRemark OK " + JSON.stringify(data));
            this.queryPatientRemark();
        })
        .catch(err => {
            console.log("createPatientRemark NG " + JSON.stringify(err));
        });
        this.setState(
            {
                EditMode: false
            }
        )
    }
    render() {
        function monthformat(month) {
            return month + 1
        }
        function dateformat(time) {
            return new Date(time).getFullYear() + "-" + monthformat(new Date(time).getMonth()) + "-" + new Date(time).getDate()
        }


        return (
            <div style={{ marginBottom: "10px" }}>
                <div className='bedsidecardtitle'>
                    <t1>床頭卡註記</t1>
                    <div onClick={() => this.onChangeEditMode()}>
                        <img src={editlogo} alt="editlogo" />
                    </div>
                </div>
                <div className='bedsidecardcontext'>
                    <t2>{this.state.remarkContent}</t2>
                </div>
                <div style={{ display: this.state.EditMode ? "block" : "none" }}>
                    <div style={{ padding: "5px 10px", backgroundColor: "rgba(238,238,238,1)" }}>
                        <Input placeholder="輸入當日註記內容" value={this.state.inputContent} onChange={this.onInputChange}></Input>
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "20px" }}>
                            <Button onClick={() => this.onClickCancel()}>取消</Button>
                            <Button onClick={() => this.onClickEnter()} type="primary">儲存</Button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Bedsidecard