import React, { Component } from 'react';
import detaillogo from '../Image/svg/details.svg'
import { Tooltip } from 'antd';
import './Schedule_event.scss'
import {yyyymmddformat , hhmmssformat} from '../Function/Timeformat'
//資料
// import { eventdata } from '../jsonResponse'
import { eventData } from '../Data/Wardviewdata-jack'

class Schedule_event_list extends Component {
    state = {
        detailstate: "none",
        pressitemstate: "all",
        items_interval: 0,
        scheduleeventdata: eventData
    }
    timechange(timestamp) {
        const changetime = new Date(timestamp).getHours() + ":" + new Date(timestamp).getMinutes()
        return changetime
    }


    pressbtn(pressitem) {
        this.setState(
            {
                pressitemstate: pressitem
            }
        );
    }
    listevent() {
        // var eventdatalist = eventdata
        const eventarray = this.state.scheduleeventdata
        const eventstate = this.state.pressitemstate

        function itemchange(changeitem) {
            switch (changeitem) {
                case "event":
                    return "事"
                case "schedule":
                    return "排"
                default:
                    return ""
            }
        }

        function checkbackground(string) {
            switch (string) {
                case "schedule":
                    return "rgba(124,179,5,1)"
                case "event":
                    return "rgba(46,100,164,1)"
                default:
                    return "rgba(124,179,5,1)"
            }
        }
        var listarray = []
        for (let index = 0; index < eventarray.length; index++) {
            const info = eventarray[index];
            const text = <div>{info.writter}醫師於{yyyymmddformat(info.time)} {hhmmssformat(info.time)}更新</div>
            if (eventstate === "all") {
                listarray.push(
                    <div key={index} className='CellLayout'>
                        <div className='Context'>
                            <div style={{height:'30px', width: "30px", borderRadius: "4px", backgroundColor: checkbackground(info.item), display: "flex", justifyContent: "center", alignItems: "center" }}>
                                <t2 style={{ color: 'white' }}>{itemchange(info.item)}</t2>
                            </div>
                            <div style={{ fontSize: "16px" }}>床{info.bednumber}/{info.name} {hhmmssformat(info.time)} {info.result}</div>
                        </div>
                        <Tooltip placement="right" title={text}>
                            <div>
                                <img src={detaillogo} alt='detaillogo'></img>
                            </div>
                        </Tooltip>
                    </div>
                )
            }
            else {
                if (eventstate === info.item) {
                    listarray.push(
                        <div key={index} className='CellLayout'>
                            <div className='Context'>
                                <div style={{height:'30px', width: "30px", borderRadius: "4px", backgroundColor: checkbackground(info.item), display: "flex", justifyContent: "center", alignItems: "center" }}>
                                    <t2 style={{ color: 'white' }}>{itemchange(info.item)}</t2>
                                </div>
                                <div style={{ fontSize: "16px" }}>床{info.bednumber}/{info.name} {hhmmssformat(info.time)} {info.result}</div>
                            </div>
                            <Tooltip placement="right" title={text}>
                                <div>
                                    <img src={detaillogo} alt='detaillogo'></img>
                                </div>
                            </Tooltip>
                        </div>
                    )
                }
            }
        }
        return listarray
    }

    // eventdata() {
    //     //eventdata 假資料
    //     var eventdatalist = eventdata
    //     var Eventstate = this.state.pressitemstate
    //     return this.listevent(eventdatalist, Eventstate)
    // }

    //測驗項目功能_區間按鈕樣式
    SwitchTestItemInterval(testiteminterval) {
        const stylestate = this.state.pressitemstate
        if (testiteminterval === stylestate) {
            return 'buttonselect'
        }
        else return 'buttonUnselect'
    }
    //測驗項目功能_區間按鈕選擇
    Onchangetimeinterval(changestate) {
        this.setState(
            {
                items_interval: changestate
            }
        )
    }
    render() {
        const { title } = this.props
        return (
            <div className='ScheduleEventLayout'>
                <div className='ScheduleEventTitle'>
                    <t2>{title}</t2>
                </div>
                <div className='ScheduleEventContext'>
                    <div className='ContextControl'>
                        <div className='buttonGroup'>
                            <div className={this.SwitchTestItemInterval("all")} onClick={() => this.pressbtn("all")}>
                                <t2 className='titlecolor'>全部</t2>
                            </div>
                            <div className={this.SwitchTestItemInterval("schedule")} onClick={() => this.pressbtn("schedule")}>
                                <t2 className='titlecolor'>排程</t2>

                            </div>
                            <div className={this.SwitchTestItemInterval("event")} onClick={() => this.pressbtn("event")}>
                                <t2 className='titlecolor'>事件</t2>
                            </div>
                        </div>
                    </div>
                    <div className='ContextList'>
                        {this.listevent()}
                    </div>
                </div>
            </div>

        );
    }
}

export default Schedule_event_list